import React from 'react';
import { formatDate } from '../../lib/dateUtils';
import { getWeekInfo, getMondayFromGlobalWeek, addDays } from '../../lib/scheduling';

interface WeekDisplayProps {
    weekInfo?: ReturnType<typeof getWeekInfo>;
    globalWeekNumber?: number;
}

const WeekDisplay: React.FC<WeekDisplayProps> = ({ weekInfo, globalWeekNumber }) => {
    // If weekInfo is not provided, calculate it from globalWeekNumber
    const displayWeekInfo = weekInfo || (globalWeekNumber ? getWeekInfo(getMondayFromGlobalWeek(globalWeekNumber)) : null);
    
    if (!displayWeekInfo) {
        return null;
    }

    return (
        <>
            <span className="fw-medium">
                Week {displayWeekInfo.weekOfYear} of {displayWeekInfo.year}
            </span>
            <small className="text-muted ms-2">
                (
                {formatDate(displayWeekInfo.mondayOfWeek)} -{' '}
                {formatDate(addDays(displayWeekInfo.mondayOfWeek, 6))}
                )
            </small>
        </>
    );
};

export default WeekDisplay;