import React, {useState} from 'react';
import {
    Button,
    Form,
    FormGroup,
    Modal,
    ModalBody,
    ModalFooter,
    ModalHeader
} from 'reactstrap';
import {Controller, useForm} from 'react-hook-form';
import TimeZoneSelect from '../../utility/TimeZoneSelect';
import DisplayFormError from '../../utility/DisplayFormError';
import ErrorDisplay from '../../utility/ErrorDisplay';
import {useChangeProjectTimeZone} from '../../../api/projects/useProjectMutations.ts';
import ProblemDetailsError from '../../../api/ProblemDetailsError';
import {ProjectStatus} from '../../../types';

interface ChangeTimeZoneFormData {
    timeZone: string;
    changeReason: string;
}

interface ChangeProjectTimeZoneModalProps {
    isOpen: boolean;
    onClose: () => void;
    projectId: string;
    currentTimeZone: string;
    projectStatus: ProjectStatus;
    onSuccess?: () => void;
}

const ChangeProjectTimeZoneModal: React.FC<ChangeProjectTimeZoneModalProps> = ({
    isOpen,
    onClose,
    projectId,
    currentTimeZone,
    projectStatus,
    onSuccess
}) => {
    const [error, setError] = useState<Error | ProblemDetailsError | null>(
        null
    );

    const {control, handleSubmit, reset} = useForm<ChangeTimeZoneFormData>({
        defaultValues: {
            timeZone: currentTimeZone,
            changeReason: ''
        }
    });

    const changeTimeZone = useChangeProjectTimeZone();

    const onSubmit = async (data: ChangeTimeZoneFormData) => {
        setError(null);

        changeTimeZone.mutate(
            {
                projectId,
                timeZone: data.timeZone,
                changeReason: data.changeReason
            },
            {
                onSuccess: () => {
                    reset();
                    onClose();
                    if (onSuccess) {
                        onSuccess();
                    }
                },
                onError: error => {
                    console.error('Error changing timezone:', error);
                    setError(error);
                }
            }
        );
    };

    const handleClose = () => {
        reset();
        setError(null);
        onClose();
    };

    return (
        <Modal isOpen={isOpen} toggle={handleClose}>
            <ModalHeader toggle={handleClose}>
                Change Project Timezone
            </ModalHeader>
            <Form onSubmit={handleSubmit(onSubmit)}>
                <ModalBody>
                    <ErrorDisplay error={error} />

                    <FormGroup>
                        <Controller
                            name="timeZone"
                            control={control}
                            rules={{
                                required: 'Time zone is required'
                            }}
                            render={({field, fieldState}) => (
                                <>
                                    <TimeZoneSelect
                                        value={field.value}
                                        onChange={field.onChange}
                                        className="mb-3"
                                        label="New Timezone"
                                    />
                                    {fieldState.error && (
                                        <DisplayFormError
                                            error={fieldState.error}
                                            fieldDisplayName="Time Zone"
                                        />
                                    )}
                                </>
                            )}
                        />
                    </FormGroup>

                    <FormGroup>
                        <Controller
                            name="changeReason"
                            control={control}
                            rules={{
                                required:
                                    projectStatus !== ProjectStatus.Draft
                                        ? 'Change reason is required'
                                        : false
                            }}
                            render={({field, fieldState}) => (
                                <>
                                    <label
                                        htmlFor="changeReason"
                                        className="form-label"
                                    >
                                        Reason for Change{' '}
                                        {projectStatus !== ProjectStatus.Draft
                                            ? '*'
                                            : ''}
                                    </label>
                                    <textarea
                                        {...field}
                                        id="changeReason"
                                        className="form-control"
                                        rows={3}
                                        placeholder={
                                            projectStatus ===
                                            ProjectStatus.Draft
                                                ? 'Optional: Provide a reason for changing the timezone...'
                                                : 'Please provide a reason for changing the timezone...'
                                        }
                                    />
                                    {fieldState.error && (
                                        <DisplayFormError
                                            error={fieldState.error}
                                            fieldDisplayName="Change Reason"
                                        />
                                    )}
                                </>
                            )}
                        />
                    </FormGroup>
                </ModalBody>
                <ModalFooter>
                    <Button color="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                    <Button
                        color="primary"
                        type="submit"
                        disabled={changeTimeZone.isPending}
                    >
                        {changeTimeZone.isPending
                            ? 'Changing...'
                            : 'Change Timezone'}
                    </Button>
                </ModalFooter>
            </Form>
        </Modal>
    );
};

export default ChangeProjectTimeZoneModal;
