import {ProjectAllocationModel} from '../../types';
import {UseQueryOptions, UseQueryResult, useQuery} from '@tanstack/react-query';
import baseUrl from '../baseUrl';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';

type AllocationsQueryOptions = Omit<
    UseQueryOptions<
        ProjectAllocationModel[],
        Error | ProblemDetailsError,
        ProjectAllocationModel[],
        readonly unknown[]
    >,
    'queryKey' | 'queryFn'
>;

const useGetProjectAllocations = (
    projectId: string,
    options?: AllocationsQueryOptions
): UseQueryResult<ProjectAllocationModel[], Error> => {
    const url = `${baseUrl}/api/projects/${projectId}/allocations`;
    const queryKey = ['project-allocations', projectId];

    const authenticatedFetch = useCreateAuthenticatedFetch();

    return useQuery({
        queryKey,
        queryFn: async () => {
            const response = await authenticatedFetch(url);
            return handleResponse<ProjectAllocationModel[]>(response);
        },
        enabled: !!projectId,
        ...options
    });
};

export default useGetProjectAllocations;
