import {EmptyResult} from '../../types';
import {UseMutationOptions, useMutation} from '@tanstack/react-query';
import baseUrl from '../baseUrl';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';

export interface DeleteAllocationParams {
    allocationId: string;
    changeReason?: string;
}

type DeleteAllocationMutationOptions = Omit<
    UseMutationOptions<
        EmptyResult,
        Error | ProblemDetailsError,
        DeleteAllocationParams,
        unknown
    >,
    'mutationFn'
>;

const useDeleteProjectAllocation = (
    options?: DeleteAllocationMutationOptions
) => {
    const authenticatedFetch = useCreateAuthenticatedFetch();

    return useMutation({
        mutationFn: async ({
            allocationId,
            changeReason
        }: DeleteAllocationParams) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/allocations/${allocationId}`,
                {
                    method: 'DELETE',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify({changeReason})
                }
            );
            return handleResponse<EmptyResult>(response);
        },
        ...options
    });
};

export default useDeleteProjectAllocation;
