import React, {useEffect, useMemo, useState} from 'react';
import {useNavigate} from 'react-router-dom';
import {
    Alert,
    Button,
    Col,
    Form,
    FormGroup,
    FormText,
    Input,
    Label,
    Row,
    Table
} from 'reactstrap';
import PersonSelector from '../../utility/PersonSelector';
import TagSelector from '../../utility/TagSelector';
import TimeZoneSelect from '../../utility/TimeZoneSelect';
import WeekSelector from '../../utility/WeekSelector';
import {
    getWeekInfo,
    getMondayFromGlobalWeek,
    addDays,
    WeekInfo
} from '../../../lib/scheduling';
import ReasonForChangeModal from './ReasonForChangeModal.tsx';
import ChangeProjectTimeZoneModal from './ChangeProjectTimeZoneModal.tsx';
import useConfirm from '../../../hooks/useConfirm';
import {useAuthStateManager} from '../../../hooks/useAuthStateManager';
import {
    ProjectModel,
    ProjectStatus,
    ProjectVisibility,
    TagModel,
    EngagementContext
} from '../../../types.ts';
import {
    CreateProjectCommand,
    useCreateProject,
    useUpdateProject,
    useUpdateProjectStatus,
    useUpdateProjectVisibility
} from '../../../api/projects/useProjectMutations.ts';
import {useGetTags} from '../../../api/tags/useGetTags.ts';

interface ProjectEditProps {
    project?: ProjectModel;
    onSave?: () => void;
    onCancel?: () => void;
}

const ProjectEdit: React.FC<ProjectEditProps> = ({
    project,
    onSave,
    onCancel
}) => {
    const navigate = useNavigate();
    const {confirm, confirmModal} = useConfirm();
    const {personId} = useAuthStateManager();
    // State for ReasonForChangeModal and status change
    const [isReasonModalOpen, setIsReasonModalOpen] = useState(false);
    const [isTimeZoneModalOpen, setIsTimeZoneModalOpen] = useState(false);
    const [pendingStatus, setPendingStatus] = useState<ProjectStatus | null>(
        null
    );
    const updateStatus = useUpdateProjectStatus();
    const updateVisibility = useUpdateProjectVisibility();
    const [pendingVisibility, setPendingVisibility] =
        useState<ProjectVisibility | null>(null);

    const {
        mondayOfWeek: defaultStartOn,
        globalWeekNumber: defaultGlobalWeekNumber
    } = getWeekInfo(new Date());
    const defaultStartOnDateString = defaultStartOn.toISOString().split('T')[0];

    const [formData, setFormData] = useState<Partial<CreateProjectCommand>>({
        name: '',
        description: '',
        timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
        startOn: defaultStartOnDateString,
        durationInWeeks: 1,
        engagementContext: EngagementContext.BAU,
        people: []
    });
    const [startGlobalWeekNumber, setStartGlobalWeekNumber] = useState(
        defaultGlobalWeekNumber
    );
    const [projectTags, setProjectTags] = useState<TagModel[]>([]);
    const [error, setError] = useState<string | null>(null);

    const {isLoading: isLoadingTags} = useGetTags(true);

    // Reset form when project changes
    useEffect(() => {
        if (project) {
            setFormData({
                name: project.name,
                description: project.description,
                timeZone:
                    project.timeZone ||
                    Intl.DateTimeFormat().resolvedOptions().timeZone,
                startOn: project.startOn.split('T')[0],
                durationInWeeks: project.durationInWeeks,
                engagementContext: project.engagementContext || EngagementContext.BAU,
                people: project.people.map(p => ({
                    personId: p.personId,
                    projectRole: p.projectRole,
                    canEdit: p.canEdit,
                    canEditResources: p.canEditResources
                }))
            });
            setProjectTags(project.tags || []);
            // Set the global week number from the project's start date
            try {
                const weekInfo = getWeekInfo(new Date(project.startOn));
                setStartGlobalWeekNumber(weekInfo.globalWeekNumber);
            } catch (error) {
                console.error(
                    'Error getting week info for project start date:',
                    error
                );
            }
        } else {
            setFormData({
                name: '',
                description: '',
                timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
                startOn: defaultStartOnDateString,
                durationInWeeks: 1,
                engagementContext: EngagementContext.BAU,
                people: []
            });
            setProjectTags([]);
            setStartGlobalWeekNumber(defaultGlobalWeekNumber);
        }
        setError(null);
    }, [project, defaultGlobalWeekNumber, defaultStartOnDateString]);

    // Mutations
    const createProject = useCreateProject();
    const updateProject = useUpdateProject();

    // Use useMemo to derive the end week information whenever the start or duration changes.
    const endWeekDetails: {info: WeekInfo; sunday: Date} | null =
        useMemo(() => {
            const duration = formData.durationInWeeks;
            if (!startGlobalWeekNumber || !duration || duration < 1) {
                return null;
            }
            try {
                // Calculate end week by adding duration (minus 1, as start week is week 1 of duration)
                const endGlobalWeekNumber =
                    startGlobalWeekNumber + duration - 1;
                const info = getWeekInfo(
                    getMondayFromGlobalWeek(endGlobalWeekNumber)
                );
                const sunday = addDays(info.mondayOfWeek, 6); // Monday is day 0, Sunday is day 6
                return {info, sunday};
            } catch (error) {
                console.error('Could not calculate project end week', error);
                return null;
            }
        }, [startGlobalWeekNumber, formData.durationInWeeks]);

    // Helper function to actually submit the form
    const submitForm = async () => {
        try {
            const formattedData = {
                ...formData,
                startOn: formData.startOn
                    ? new Date(formData.startOn).toISOString()
                    : undefined,
                durationInWeeks: formData.durationInWeeks || 1,
                tags: projectTags
            };

            if (project && project.id) {
                await updateProject.mutateAsync({
                    ...(formattedData as CreateProjectCommand),
                    id: project.id
                });
            } else {
                const savedProject = await createProject.mutateAsync(
                    formattedData as CreateProjectCommand
                );
                const newId = savedProject?.id;
                if (newId) {
                    navigate(`/project/${newId}`);
                }
                if (onSave) {
                    onSave();
                }
            }
        } catch (err) {
            console.error('Error saving project:', err);
            setError('Failed to save project. Please try again.');
        }
    };

    // Form submission
    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError(null);

        if (
            !formData.name ||
            !formData.timeZone ||
            !formData.startOn ||
            !formData.durationInWeeks
        ) {
            setError('Please fill in all required fields');
            return;
        }

        // Validation for editor removal
        if (project && project.id && personId) {
            const currentPeople = formData.people || [];
            const wasCurrentUserAnEditor = project.people.some(
                p => p.personId === personId && p.canEdit
            );
            const currentUserInNewList = currentPeople.find(
                p => p.personId === personId
            );
            const isCurrentUserStillEditor =
                currentUserInNewList?.canEdit || false;

            // Check if current user is removing themselves as the only editor
            if (
                wasCurrentUserAnEditor &&
                !isCurrentUserStillEditor &&
                currentPeople.filter(p => p.canEdit).length === 0
            ) {
                setError(
                    'You cannot remove yourself as the only editor. Another editor must be assigned before you can remove yourself, or another editor can remove you.'
                );
                return;
            }

            // Check if current user is removing themselves but there are other editors - show confirmation
            if (
                wasCurrentUserAnEditor &&
                !isCurrentUserStillEditor &&
                currentPeople.filter(p => p.canEdit).length > 0
            ) {
                const warningMessage = `Are you sure you want to remove yourself as an editor? You will no longer have edit access to this project${
                    project.status === ProjectStatus.Draft ||
                    project.visibility === ProjectVisibility.NonDisclosed
                        ? ', and you may not be able to see it if it remains in draft status or non-disclosed'
                        : ''
                }.`;

                confirm(warningMessage, submitForm);
                return;
            }
        }

        await submitForm();
    };

    const handleTagsChange = (newTags: TagModel[]) => {
        setProjectTags(newTags);
    };

    return (
        <>
            <Form
                onSubmit={handleSubmit}
                className="p-4 border rounded bg-light"
            >
                {error && <Alert color="danger">{error}</Alert>}
                {/* Top-right action buttons */}
                <div className="d-flex justify-content-between align-items-start mb-4">
                    <div></div>
                    <div>
                        {project && project.id && (
                            <>
                                <Button
                                    color="success"
                                    outline
                                    className="me-2"
                                    disabled={updateStatus.isPending}
                                    onClick={() => {
                                        setPendingStatus(
                                            project.status ===
                                                ProjectStatus.Draft
                                                ? ProjectStatus.Published
                                                : ProjectStatus.Draft
                                        );
                                        setIsReasonModalOpen(true);
                                    }}
                                >
                                    {project.status === ProjectStatus.Draft
                                        ? 'Publish'
                                        : 'Revert to Draft'}
                                </Button>
                                <Button
                                    color="warning"
                                    outline
                                    disabled={updateVisibility.isPending}
                                    onClick={() => {
                                        setPendingVisibility(
                                            project.visibility ===
                                                ProjectVisibility.Disclosed
                                                ? ProjectVisibility.NonDisclosed
                                                : ProjectVisibility.Disclosed
                                        );
                                        setIsReasonModalOpen(true);
                                    }}
                                >
                                    {project.visibility ===
                                    ProjectVisibility.Disclosed
                                        ? 'Make Non-Disclosed'
                                        : 'Make Disclosed'}
                                </Button>
                            </>
                        )}
                    </div>
                </div>

                <Row className="mb-3">
                    <Col md={8}>
                        <FormGroup>
                            <Label for="name">Name *</Label>
                            <Input
                                id="name"
                                value={formData.name || ''}
                                onChange={e =>
                                    setFormData(prev => ({
                                        ...prev,
                                        name: e.target.value
                                    }))
                                }
                                required
                            />
                        </FormGroup>
                    </Col>
                    <Col md={4}>
                        <FormGroup>
                            <Label>Engagement Context</Label>
                            <Input
                                type="select"
                                value={formData.engagementContext}
                                onChange={e =>
                                    setFormData(prev => ({
                                        ...prev,
                                        engagementContext: parseInt(
                                            e.target.value
                                        ) as EngagementContext
                                    }))
                                }
                            >
                                <option value={EngagementContext.BAU}>
                                    Business As Usual (BAU)
                                </option>
                                <option value={EngagementContext.NonBAU}>
                                    Non-Business As Usual (Non-BAU)
                                </option>
                            </Input>
                            <FormText color="muted">
                                <strong>BAU:</strong> Routine operational work or standard business activities.<br />
                                <strong>Non-BAU:</strong> Change initiatives, strategic projects, or transformation efforts that are outside normal operations.
                            </FormText>
                        </FormGroup>
                    </Col>
                </Row>

                <FormGroup>
                    <Label for="description">Description</Label>
                    <Input
                        id="description"
                        type="textarea"
                        value={formData.description || ''}
                        onChange={e =>
                            setFormData(prev => ({
                                ...prev,
                                description: e.target.value
                            }))
                        }
                        rows={3}
                    />
                </FormGroup>

                <FormGroup>
                    {project && project.id ? (
                        <>
                            <div className="d-flex justify-content-between align-items-center">
                                <Label>Timezone</Label>
                                {project.status === ProjectStatus.Draft && (
                                    <Button
                                        color="link"
                                        size="sm"
                                        onClick={() =>
                                            setIsTimeZoneModalOpen(true)
                                        }
                                        className="p-0"
                                    >
                                        Change
                                    </Button>
                                )}
                            </div>
                            <Input
                                value={formData.timeZone || ''}
                                disabled
                                className="mb-3"
                            />
                            {project.status !== ProjectStatus.Draft && (
                                <small className="text-muted">
                                    Timezone can only be changed when project is
                                    in Draft status
                                </small>
                            )}
                        </>
                    ) : (
                        <TimeZoneSelect
                            value={formData.timeZone || ''}
                            onChange={value =>
                                setFormData(prev => ({
                                    ...prev,
                                    timeZone: value
                                }))
                            }
                            label="Timezone *"
                            className="mb-3"
                        />
                    )}
                </FormGroup>

                <Row className="mb-3">
                    <Col md={8}>
                        <WeekSelector
                            value={startGlobalWeekNumber}
                            onChange={(globalWeekNumber, weekInfo) => {
                                setStartGlobalWeekNumber(globalWeekNumber);
                                setFormData(prev => ({
                                    ...prev,
                                    startOn: weekInfo.mondayOfWeek
                                        .toISOString()
                                        .split('T')[0]
                                }));
                            }}
                            label="Start Week *"
                        />
                    </Col>
                    <Col md={4}>
                        <FormGroup>
                            <Label for="durationInWeeks">
                                Duration (Weeks) *
                            </Label>
                            <Input
                                type="number"
                                id="durationInWeeks"
                                value={formData.durationInWeeks || ''}
                                onChange={e =>
                                    setFormData(prev => ({
                                        ...prev,
                                        durationInWeeks:
                                            parseInt(e.target.value, 10) || 1
                                    }))
                                }
                                min="1"
                                required
                                bsSize="sm"
                            />
                            {endWeekDetails && (
                                <FormText>
                                    Ends Sun,{' '}
                                    {endWeekDetails.sunday.toLocaleDateString()}
                                    <br />
                                    <small className="text-muted">
                                        (Week {endWeekDetails.info.weekOfYear}{' '}
                                        of {endWeekDetails.info.year})
                                    </small>
                                </FormText>
                            )}
                        </FormGroup>
                    </Col>
                </Row>

                <FormGroup>
                    <Label>Project People</Label>
                    <PersonSelector
                        selectedPersonIds={[]}
                        onPersonSelectionChange={selectedIds => {
                            // Add new people to the list with default values
                            const newPeople = selectedIds.map(personId => ({
                                personId,
                                projectRole: 'Team Member',
                                canEdit: false,
                                canEditResources: false
                            }));

                            setFormData(prev => ({
                                ...prev,
                                people: [...(prev.people || []), ...newPeople]
                            }));
                        }}
                        label=""
                        placeholder="Add people to project..."
                    />

                    {formData.people && formData.people.length > 0 && (
                        <Table striped className="mt-3">
                            <thead>
                                <tr>
                                    <th>Name</th>
                                    <th>Role</th>
                                    <th>Can Edit</th>
                                    <th>Can Edit Resources</th>
                                    <th>Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {formData.people.map((person, index) => {
                                    // Find the person info from the project data
                                    const personInfo = project?.people.find(
                                        p => p.personId === person.personId
                                    )?.person;
                                    return (
                                        <tr key={person.personId}>
                                            <td>
                                                {personInfo
                                                    ? `${personInfo.firstName} ${personInfo.lastName}`
                                                    : person.personId}
                                            </td>
                                            <td>
                                                <Input
                                                    type="text"
                                                    value={person.projectRole}
                                                    onChange={e => {
                                                        const updatedPeople = [
                                                            ...(formData.people ||
                                                                [])
                                                        ];
                                                        updatedPeople[
                                                            index
                                                        ].projectRole =
                                                            e.target.value;
                                                        setFormData(prev => ({
                                                            ...prev,
                                                            people: updatedPeople
                                                        }));
                                                    }}
                                                    placeholder="Enter role..."
                                                    maxLength={100}
                                                />
                                            </td>
                                            <td>
                                                <Input
                                                    type="checkbox"
                                                    checked={person.canEdit}
                                                    onChange={e => {
                                                        const updatedPeople = [
                                                            ...(formData.people ||
                                                                [])
                                                        ];
                                                        updatedPeople[
                                                            index
                                                        ].canEdit =
                                                            e.target.checked;
                                                        setFormData(prev => ({
                                                            ...prev,
                                                            people: updatedPeople
                                                        }));
                                                    }}
                                                />
                                            </td>
                                            <td>
                                                <Input
                                                    type="checkbox"
                                                    checked={
                                                        person.canEditResources
                                                    }
                                                    onChange={e => {
                                                        const updatedPeople = [
                                                            ...(formData.people ||
                                                                [])
                                                        ];
                                                        updatedPeople[
                                                            index
                                                        ].canEditResources =
                                                            e.target.checked;
                                                        setFormData(prev => ({
                                                            ...prev,
                                                            people: updatedPeople
                                                        }));
                                                    }}
                                                />
                                            </td>
                                            <td>
                                                <Button
                                                    color="danger"
                                                    size="sm"
                                                    onClick={() => {
                                                        const updatedPeople = (
                                                            formData.people ||
                                                            []
                                                        ).filter(
                                                            (_, i) =>
                                                                i !== index
                                                        );
                                                        setFormData(prev => ({
                                                            ...prev,
                                                            people: updatedPeople
                                                        }));
                                                    }}
                                                >
                                                    Remove
                                                </Button>
                                            </td>
                                        </tr>
                                    );
                                })}
                            </tbody>
                        </Table>
                    )}
                </FormGroup>

                <FormGroup>
                    <Label>Tags</Label>
                    <TagSelector
                        selectedTags={projectTags}
                        onChange={handleTagsChange}
                        placeholder="Select or create tags..."
                        isLoading={isLoadingTags}
                        allowCreate={true}
                    />
                </FormGroup>

                <div className="d-flex justify-content-end gap-2">
                    <Button
                        color="secondary"
                        onClick={() => {
                            if (!project || !project.id) {
                                navigate('/projects');
                            } else if (onCancel) {
                                onCancel();
                            }
                        }}
                    >
                        Close Edit
                    </Button>
                    <Button
                        color="primary"
                        type="submit"
                        disabled={
                            createProject.isPending || updateProject.isPending
                        }
                    >
                        {createProject.isPending || updateProject.isPending
                            ? 'Saving...'
                            : 'Save'}
                    </Button>
                </div>
            </Form>
            {/* ReasonForChangeModal */}
            <ReasonForChangeModal
                isOpen={isReasonModalOpen}
                onClose={() => setIsReasonModalOpen(false)}
                onSubmit={async (reason: string) => {
                    if (!project || !project.id) {
                        return;
                    }

                    if (pendingStatus !== null) {
                        await updateStatus.mutateAsync({
                            projectId: project.id,
                            newStatus: pendingStatus,
                            changeReason: reason
                        });
                        setPendingStatus(null);
                    } else if (pendingVisibility !== null) {
                        await updateVisibility.mutateAsync({
                            projectId: project.id,
                            newVisibility: pendingVisibility,
                            changeReason: reason
                        });
                        setPendingVisibility(null);
                    }
                    setIsReasonModalOpen(false);
                }}
                description={
                    pendingStatus
                        ? pendingStatus === ProjectStatus.Published
                            ? 'publish this project'
                            : 'revert this project to Draft'
                        : pendingVisibility !== null
                          ? pendingVisibility === ProjectVisibility.Disclosed
                              ? 'make this project Disclosed'
                              : 'make this project Non-Disclosed'
                          : ''
                }
                isPending={updateStatus.isPending || updateVisibility.isPending}
                projectStatus={project?.status}
            />

            {/* ChangeProjectTimeZoneModal */}
            {project && project.id && (
                <ChangeProjectTimeZoneModal
                    isOpen={isTimeZoneModalOpen}
                    onClose={() => setIsTimeZoneModalOpen(false)}
                    projectId={project.id}
                    currentTimeZone={formData.timeZone || ''}
                    projectStatus={project.status}
                />
            )}
            {confirmModal}
        </>
    );
};

export default ProjectEdit;
