/**
 * Default phase colors based on position
 * These colors are derived from the dashboard sample data and provide
 * a consistent color scheme for phases across the application
 */
export const DEFAULT_PHASE_COLORS = [
    '#4F46E5', // Indigo - Phase 1
    '#7C3AED', // Purple - Phase 2  
    '#DB2777', // Pink - Phase 3
    '#DC2626', // Red - Phase 4
    '#EA580C', // Orange - Phase 5
    '#16A34A', // Green - Phase 6
    '#0891B2', // Cyan - Phase 7
    '#8B5CF6', // Violet - Phase 8
    '#F59E0B', // Amber - Phase 9
    '#10B981', // Emerald - Phase 10
    '#3B82F6', // Blue - Phase 11
    '#EF4444', // Red variant - Phase 12
] as const;

/**
 * Gets the default color for a phase based on its position (0-indexed)
 * If the position exceeds available colors, it cycles through the color array
 * @param position - The 0-based position of the phase
 * @returns The hex color code for the phase
 */
export const getDefaultPhaseColor = (position: number): string => {
    if (position < 0) {
        return DEFAULT_PHASE_COLORS[0];
    }
    
    return DEFAULT_PHASE_COLORS[position % DEFAULT_PHASE_COLORS.length];
};

/**
 * Gets the default color for a phase based on its position in an array of phases
 * @param phases - Array of existing phases
 * @returns The hex color code for the next phase
 */
export const getNextPhaseColor = (phases: Array<{ colour?: string }>): string => {
    return getDefaultPhaseColor(phases.length);
};