// ========================
// Case Conversion Utilities
// Note: All case conversion functions require `toKebab` to work.
// ------------------------
// https://github.com/zellwk/javascript/tree/master/src/utils/convert-case

export function toKebabCase(string: string) {
    return string
        .trim()
        .replace(/([a-z])([A-Z])/g, '$1-$2')
        .replace(/\W|_/g, m => (/[-]/.test(m) ? m : '-'))
        .replace(/^-+|-+$/g, '')
        .replace(/-{2,}/g, '-')
        .toLowerCase();
}

export function toCamelCase(string: string) {
    return toKebabCase(string)
        .split('-')
        .map((word, index) => {
            if (index === 0) return word;
            return word.slice(0, 1).toUpperCase() + word.slice(1).toLowerCase();
        })
        .join('');
}

export function toTitleCase(string: string) {
    return toKebabCase(string)
        .split('-')
        .map(word => {
            return word.slice(0, 1).toUpperCase() + word.slice(1);
        })
        .join(' ');
}

export function toSentenceCase(string: string) {
    const interim = toKebabCase(string).replace(/-/g, ' ');
    return interim.slice(0, 1).toUpperCase() + interim.slice(1);
}

const capitalize = (string: string) =>
    string.charAt(0).toUpperCase() + string.slice(1);

export function breakToWords(string: string) {
    return capitalize(toKebabCase(string).replace(/-/g, ' '));
}

export function toSnakeCase(string: string) {
    return toKebabCase(string).split('-').join('_');
}
