import {
    useMutation,
    useQueryClient,
    UseMutationResult
} from '@tanstack/react-query';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';
import {
    ProjectStatus,
    ProjectVisibility,
    IdResult,
    EmptyResult,
    TagModel,
    EditProjectPersonModel,
    EngagementContext
} from '../../types.ts';

export interface CreateProjectCommand {
    name: string;
    description?: string;
    timeZone: string; // IANA timezone
    startOn: string; // DateTime when the project starts
    durationInWeeks: number; // Duration in weeks
    engagementContext: EngagementContext;
    people: EditProjectPersonModel[];
    tags?: TagModel[];
}

interface UpdateProjectCommand extends CreateProjectCommand {
    id: string;
    changeReason?: string;
}

export const useCreateProject = (): UseMutationResult<
    IdResult,
    Error | ProblemDetailsError,
    CreateProjectCommand
> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();
    const queryClient = useQueryClient();

    return useMutation({
        mutationFn: async (command: CreateProjectCommand) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/projects`,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(command)
                }
            );
            return handleResponse<IdResult>(response);
        },
        onSuccess: () => {
            queryClient.invalidateQueries({queryKey: ['projects']});
        }
    });
};

export const useUpdateProject = (): UseMutationResult<
    EmptyResult,
    Error | ProblemDetailsError,
    UpdateProjectCommand
> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();
    const queryClient = useQueryClient();

    return useMutation({
        mutationFn: async (command: UpdateProjectCommand) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/projects/${command.id}`,
                {
                    method: 'PUT',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(command)
                }
            );
            return handleResponse<EmptyResult>(response);
        },
        onSuccess: () => {
            queryClient.invalidateQueries({queryKey: ['projects']});
        }
    });
};

export const useDeleteProject = (): UseMutationResult<
    void,
    Error | ProblemDetailsError,
    string
> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();
    const queryClient = useQueryClient();

    return useMutation({
        mutationFn: async (projectId: string) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/projects/${projectId}`,
                {
                    method: 'DELETE'
                }
            );
            return handleResponse<void>(response);
        },
        onSuccess: () => {
            queryClient.invalidateQueries({queryKey: ['projects']});
        }
    });
};

