import React, {useEffect, useState} from 'react';
import {
    Button,
    Input,
    Alert,
    Spinner,
    Form,
    FormGroup,
    Label,
    Row,
    Col
} from 'reactstrap';
import {
    ChevronDown,
    ChevronRight,
    Edit,
    Plus,
    Save,
    Trash2,
    X,
    User,
    Users,
    Calendar
} from 'lucide-react';
import {
    getWeekInfo,
    getMondayFromGlobalWeek,
    addDays
} from '../../../lib/scheduling';
import PersonSelector from '../../utility/PersonSelector';
import ErrorDisplay from '../../utility/ErrorDisplay.tsx';
import FieldError from '../../utility/FieldError';
import WeekDisplay from '../../utility/WeekDisplay';
import ReasonForChangeModal from './ReasonForChangeModal.tsx';
import useGetProjectAllocations from '../../../api/projectAllocations/useGetProjectAllocations.ts';
import useDeleteProjectAllocation from '../../../api/projectAllocations/useDeleteProjectAllocation.ts';
import useGetPagedTeams from '../../../api/teams/useGetPagedTeams';
import {
    useBulkUpdateAllocation,
    ProjectAllocationUpdateItem,
    BulkAllocationUpdateCommand
} from '../../../api/projects/useBulkUpdateAllocation.ts';
import {
    ProjectModel,
    ProjectAllocationModel,
    AllocationType,
    EngagementContext
} from '../../../types.ts';

interface EditableAllocation extends ProjectAllocationModel {
    isNew?: boolean;
    isEditing?: boolean;
    hasChanges?: boolean;
    originalData?: Partial<ProjectAllocationModel>;
    index: number;
    assignmentType: 'person' | 'team';
}

interface WeekAllocation {
    globalWeekNumber: number;
    weekInfo: ReturnType<typeof getWeekInfo>;
    allocations: EditableAllocation[];
    isExpanded: boolean;
    isEditing: boolean;
}

interface AllocationSectionProps {
    project: ProjectModel;
    canCurrentUserEdit: boolean;
}

const AllocationEditForm: React.FC<{
    allocation: EditableAllocation;
    project: ProjectModel;
    teamsData: any;
    bulkUpdateAllocation: any;
    onUpdateField: (
        globalWeekNumber: number,
        allocationId: string,
        field: string,
        value: any
    ) => void;
    onDelete: (allocationId: string) => void;
    deleteAllocation: any;
}> = ({
    allocation,
    project,
    teamsData,
    bulkUpdateAllocation,
    onUpdateField,
    onDelete,
    deleteAllocation
}) => {
    const teamOptions =
        teamsData?.items?.map((team: any) => ({
            value: team.id,
            label: team.name
        })) || [];

    return (
        <Form className="p-3 bg-light border-top">
            <Row>
                <Col md={6}>
                    <FormGroup>
                        <Label>Assignment Type</Label>
                        <Input
                            type="select"
                            value={allocation.assignmentType}
                            onChange={e => {
                                const assignmentType = e.target.value as
                                    | 'person'
                                    | 'team';
                                onUpdateField(
                                    allocation.globalWeekNumber,
                                    allocation.id,
                                    'assignmentType',
                                    assignmentType
                                );
                                if (assignmentType === 'person') {
                                    onUpdateField(
                                        allocation.globalWeekNumber,
                                        allocation.id,
                                        'teamId',
                                        undefined
                                    );
                                } else {
                                    onUpdateField(
                                        allocation.globalWeekNumber,
                                        allocation.id,
                                        'personId',
                                        undefined
                                    );
                                }
                            }}
                            disabled={!allocation.isNew}
                        >
                            <option value="person">Person</option>
                            <option value="team">Team</option>
                        </Input>
                        <FieldError
                            path={`allocations.${allocation.index}.resource`}
                            error={bulkUpdateAllocation.error}
                        />
                        {!allocation.isNew && (
                            <small className="text-muted mt-1 d-block">
                                Assignment type cannot be changed for existing
                                records. Delete this allocation and create a new
                                one to change the assignment type.
                            </small>
                        )}
                    </FormGroup>

                    {allocation.assignmentType === 'person' ? (
                        <>
                            <PersonSelector
                                selectedPersonIds={
                                    allocation.personId
                                        ? [allocation.personId]
                                        : []
                                }
                                onPersonSelectionChange={selectedIds => {
                                    onUpdateField(
                                        allocation.globalWeekNumber,
                                        allocation.id,
                                        'personId',
                                        selectedIds[0] || undefined
                                    );
                                }}
                                label="Person"
                                placeholder="Select a person"
                                disabled={!allocation.isNew}
                            />
                            {!allocation.isNew && (
                                <small className="text-muted mt-1 d-block">
                                    Person cannot be changed for existing
                                    records. Delete this allocation and create a
                                    new one to change the person.
                                </small>
                            )}
                        </>
                    ) : (
                        <FormGroup>
                            <Label>Team</Label>
                            <Input
                                type="select"
                                value={allocation.teamId || ''}
                                onChange={e =>
                                    onUpdateField(
                                        allocation.globalWeekNumber,
                                        allocation.id,
                                        'teamId',
                                        e.target.value || undefined
                                    )
                                }
                                disabled={!allocation.isNew}
                            >
                                <option value="">Select a team</option>
                                {teamOptions.map((team: any) => (
                                    <option key={team.value} value={team.value}>
                                        {team.label}
                                    </option>
                                ))}
                            </Input>
                            <FieldError
                                path={`allocations.${allocation.index}.teamId`}
                                error={bulkUpdateAllocation.error}
                            />
                            {!allocation.isNew && (
                                <small className="text-muted mt-1 d-block">
                                    Team cannot be changed for existing records.
                                    Delete this allocation and create a new one
                                    to change the team.
                                </small>
                            )}
                        </FormGroup>
                    )}
                </Col>

                <Col md={6}>
                    <Row>
                        <Col md={6}>
                            <FormGroup>
                                <Label>Week</Label>
                                <div className="form-control-plaintext">
                                    <WeekDisplay
                                        globalWeekNumber={
                                            allocation.globalWeekNumber
                                        }
                                    />
                                </div>
                                <FieldError
                                    path={`allocations.${allocation.index}.globalWeekNumber`}
                                    error={bulkUpdateAllocation.error}
                                />
                            </FormGroup>
                        </Col>
                        <Col md={6}></Col>
                    </Row>

                    <Row>
                        <Col md={6}>
                            <FormGroup>
                                <Label>Business As Usual Hours</Label>
                                <Input
                                    type="number"
                                    value={allocation.hoursBAU}
                                    onChange={e =>
                                        onUpdateField(
                                            allocation.globalWeekNumber,
                                            allocation.id,
                                            'hoursBAU',
                                            parseFloat(e.target.value) || 0
                                        )
                                    }
                                    min="0"
                                    step="0.25"
                                />
                                <small className="text-muted">
                                    Regular operational work and ongoing
                                    activities
                                </small>
                                <FieldError
                                    path={`allocations.${allocation.index}.hoursBAU`}
                                    error={bulkUpdateAllocation.error}
                                />
                            </FormGroup>
                        </Col>
                        {/* Only show Non-BAU Hours field if project is not BAU context, or if it already has a value */}
                        {(project.engagementContext !== EngagementContext.BAU ||
                          (allocation.hoursNonBAU && allocation.hoursNonBAU > 0)) && (
                            <Col md={6}>
                                <FormGroup>
                                    <Label>Non-Business As Usual Hours</Label>
                                    <Input
                                        type="number"
                                        value={allocation.hoursNonBAU}
                                        onChange={e =>
                                            onUpdateField(
                                                allocation.globalWeekNumber,
                                                allocation.id,
                                                'hoursNonBAU',
                                                parseFloat(e.target.value) || 0
                                            )
                                        }
                                        min="0"
                                        step="0.5"
                                    />
                                    <small className="text-muted">
                                        Project work, initiatives, and special
                                        activities
                                    </small>
                                    <FieldError
                                        path={`allocations.${allocation.index}.hoursNonBAU`}
                                        error={bulkUpdateAllocation.error}
                                    />
                                </FormGroup>
                            </Col>
                        )}
                    </Row>

                    <Row>
                        <Col md={6}>
                            <FormGroup>
                                <Label>Allocation Type</Label>
                                <Input
                                    type="select"
                                    value={allocation.allocationType}
                                    onChange={e =>
                                        onUpdateField(
                                            allocation.globalWeekNumber,
                                            allocation.id,
                                            'allocationType',
                                            parseInt(
                                                e.target.value
                                            ) as AllocationType
                                        )
                                    }
                                >
                                    <option value={AllocationType.Delivery}>
                                        Delivery
                                    </option>
                                    <option value={AllocationType.DeliveredTo}>
                                        Delivered To
                                    </option>
                                </Input>
                            </FormGroup>
                        </Col>
                        <Col md={6}></Col>
                    </Row>
                </Col>
            </Row>

            {/* Description Row - Full Width at Bottom */}
            <Row className="mt-3">
                <Col md={12}>
                    <FormGroup>
                        <Label>Description</Label>
                        <Input
                            type="textarea"
                            value={allocation.description}
                            onChange={e =>
                                onUpdateField(
                                    allocation.globalWeekNumber,
                                    allocation.id,
                                    'description',
                                    e.target.value
                                )
                            }
                            rows={3}
                        />
                        <FieldError
                            path={`allocations.${allocation.index}.description`}
                            error={bulkUpdateAllocation.error}
                        />
                    </FormGroup>
                </Col>
            </Row>

            {/* Delete button in bottom left */}
            {allocation.isEditing && !allocation.isNew && (
                <div className="mt-3 d-flex justify-content-start">
                    <Button
                        color="link"
                        size="sm"
                        className="p-1 text-danger"
                        onClick={() => onDelete(allocation.id)}
                        disabled={deleteAllocation.isPending}
                        title="Delete allocation"
                    >
                        <Trash2 size={12} className="me-1" />
                        Delete
                    </Button>
                </div>
            )}
        </Form>
    );
};

const AllocationSection: React.FC<AllocationSectionProps> = ({
    project,
    canCurrentUserEdit
}) => {
    const [weekAllocations, setWeekAllocations] = useState<WeekAllocation[]>(
        []
    );
    const [isReasonModalOpen, setIsReasonModalOpen] = useState(false);
    const [pendingUpdates, setPendingUpdates] = useState<
        ProjectAllocationUpdateItem[]
    >([]);
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
    const [allocationToDelete, setAllocationToDelete] = useState<string | null>(
        null
    );

    const {
        data: allocationsData,
        isLoading,
        error: allocationsError
    } = useGetProjectAllocations(project.id!);

    const {data: teamsData} = useGetPagedTeams(1, 100);
    const bulkUpdateAllocation = useBulkUpdateAllocation();
    const deleteAllocation = useDeleteProjectAllocation();

    // Generate week allocations for the entire project duration
    useEffect(() => {
        if (!project.startGlobalWeekNumber || !project.durationInWeeks) return;

        const weeks: WeekAllocation[] = [];
        for (let i = 0; i < project.durationInWeeks; i++) {
            const globalWeekNumber = project.startGlobalWeekNumber + i;
            const weekInfo = getWeekInfo(
                getMondayFromGlobalWeek(globalWeekNumber)
            );

            // Get allocations for this week
            const weekAllocationsData =
                allocationsData?.filter(
                    allocation =>
                        allocation.globalWeekNumber === globalWeekNumber
                ) || [];

            const editableAllocations: EditableAllocation[] =
                weekAllocationsData.map((allocation, index) => ({
                    ...allocation,
                    isNew: false,
                    isEditing: false,
                    hasChanges: false,
                    originalData: {...allocation},
                    index,
                    assignmentType: allocation.personId ? 'person' : 'team'
                }));

            weeks.push({
                globalWeekNumber,
                weekInfo,
                allocations: editableAllocations,
                isExpanded: false,
                isEditing: false
            });
        }

        setWeekAllocations(weeks);
    }, [
        project.startGlobalWeekNumber,
        project.durationInWeeks,
        allocationsData
    ]);

    const toggleWeekExpansion = (globalWeekNumber: number) => {
        setWeekAllocations(prev =>
            prev.map(week =>
                week.globalWeekNumber === globalWeekNumber
                    ? {...week, isExpanded: !week.isExpanded}
                    : week
            )
        );
    };

    const addNewAllocation = (globalWeekNumber: number) => {
        const weekInfo = getWeekInfo(getMondayFromGlobalWeek(globalWeekNumber));
        const weekStart = weekInfo.mondayOfWeek.toISOString();
        const weekEnd = addDays(weekInfo.mondayOfWeek, 6).toISOString();

        // For BAU projects, default to BAU hours instead of Non-BAU hours
        const defaultHoursBAU = project.engagementContext === EngagementContext.BAU ? 8 : 0;
        const defaultHoursNonBAU = project.engagementContext === EngagementContext.BAU ? 0 : 8;

        const newAllocation: EditableAllocation = {
            id: `temp-${Date.now()}`,
            projectId: project.id!,
            personId: undefined,
            teamId: undefined,
            person: undefined,
            team: undefined,
            description: '',
            globalWeekNumber,
            weekBeginningOn: weekStart,
            weekEndOn: weekEnd,
            hoursBAU: defaultHoursBAU,
            hoursNonBAU: defaultHoursNonBAU,
            allocationType: AllocationType.Delivery,
            createdAt: new Date().toISOString(),
            updatedAt: undefined,
            isNew: true,
            isEditing: true,
            hasChanges: true,
            originalData: {},
            index: 0,
            assignmentType: 'person'
        };

        setWeekAllocations(prev =>
            prev.map(week =>
                week.globalWeekNumber === globalWeekNumber
                    ? {
                          ...week,
                          allocations: [...week.allocations, newAllocation],
                          isExpanded: true,
                          isEditing: true
                      }
                    : week
            )
        );
    };

    const toggleAllocationEdit = (
        globalWeekNumber: number,
        allocationId: string
    ) => {
        setWeekAllocations(prev =>
            prev.map(week =>
                week.globalWeekNumber === globalWeekNumber
                    ? {
                          ...week,
                          allocations: week.allocations
                              .map(allocation => {
                                  if (allocation.id === allocationId) {
                                      if (allocation.isEditing) {
                                          // Cancel editing - revert to original data
                                          if (allocation.isNew) {
                                              return null; // Remove new allocations when canceling
                                          }
                                          return {
                                              ...allocation.originalData!,
                                              isEditing: false,
                                              hasChanges: false,
                                              originalData:
                                                  allocation.originalData
                                          } as EditableAllocation;
                                      } else {
                                          // Start editing
                                          return {
                                              ...allocation,
                                              isEditing: true,
                                              originalData: {...allocation}
                                          };
                                      }
                                  }
                                  return allocation;
                              })
                              .filter(Boolean) as EditableAllocation[],
                          isEditing: week.allocations.some(
                              a => a.isEditing && a.hasChanges
                          )
                      }
                    : week
            )
        );
    };

    const updateAllocationField = (
        globalWeekNumber: number,
        allocationId: string,
        field: string,
        value: any
    ) => {
        setWeekAllocations(prev =>
            prev.map(week =>
                week.globalWeekNumber === globalWeekNumber
                    ? {
                          ...week,
                          allocations: week.allocations.map(allocation => {
                              if (allocation.id === allocationId) {
                                  const updatedAllocation = {
                                      ...allocation,
                                      [field]: value,
                                      hasChanges: true
                                  };
                                  return updatedAllocation;
                              }
                              return allocation;
                          }),
                          isEditing: true
                      }
                    : week
            )
        );
    };

    const getChangedFields = (
        allocation: EditableAllocation
    ): ProjectAllocationUpdateItem => {
        if (allocation.isNew) {
            return {
                personId: allocation.personId,
                teamId: allocation.teamId,
                description: allocation.description,
                globalWeekNumber: allocation.globalWeekNumber,
                hoursBAU: allocation.hoursBAU,
                hoursNonBAU: allocation.hoursNonBAU,
                allocationType: allocation.allocationType,
                index: allocation.index
            };
        }

        const changes: ProjectAllocationUpdateItem = {
            id: allocation.id,
            index: allocation.index
        };

        const original = allocation.originalData!;
        if (allocation.personId !== original.personId)
            changes.personId = allocation.personId;
        if (allocation.teamId !== original.teamId)
            changes.teamId = allocation.teamId;
        if (allocation.description !== original.description)
            changes.description = allocation.description;
        if (allocation.hoursBAU !== original.hoursBAU)
            changes.hoursBAU = allocation.hoursBAU;
        if (allocation.hoursNonBAU !== original.hoursNonBAU)
            changes.hoursNonBAU = allocation.hoursNonBAU;
        if (allocation.allocationType !== original.allocationType)
            changes.allocationType = allocation.allocationType;

        return changes;
    };

    const handleUpdate = () => {
        const allocationsToUpdate = weekAllocations
            .flatMap(week => week.allocations)
            .filter(
                allocation => allocation.isEditing && allocation.hasChanges
            );

        if (allocationsToUpdate.length === 0) return;

        const updates = allocationsToUpdate.map(getChangedFields);
        setPendingUpdates(updates);
        setIsReasonModalOpen(true);
    };

    const submitUpdates = async (reason: string) => {
        if (pendingUpdates.length === 0) return;

        const command: BulkAllocationUpdateCommand = {
            projectId: project.id!,
            allocations: pendingUpdates,
            changeReason: reason
        };

        bulkUpdateAllocation.mutate(command, {
            onSuccess: () => {
                // Reset editing states
                setWeekAllocations(prev =>
                    prev.map(week => ({
                        ...week,
                        allocations: week.allocations.map(allocation => ({
                            ...allocation,
                            isEditing: false,
                            hasChanges: false,
                            originalData: {...allocation}
                        })),
                        isEditing: false
                    }))
                );

                setPendingUpdates([]);
                setIsReasonModalOpen(false);
            }
        });
    };

    const handleDeleteAllocation = (allocationId: string) => {
        setAllocationToDelete(allocationId);
        setIsDeleteModalOpen(true);
    };

    const confirmDeleteAllocation = async (reason: string) => {
        if (allocationToDelete) {
            deleteAllocation.mutate(
                {
                    allocationId: allocationToDelete,
                    changeReason: reason
                },
                {
                    onSuccess: () => {
                        setIsDeleteModalOpen(false);
                        setAllocationToDelete(null);
                    }
                }
            );
        }
    };

    const hasEditingAllocations = weekAllocations.some(week => week.isEditing);

    if (isLoading) {
        return (
            <div className="mb-4">
                <h3>Resource Allocations</h3>
                <div className="d-flex align-items-center">
                    <Spinner size="sm" className="me-2" />
                    <span className="text-muted">Loading allocations...</span>
                </div>
            </div>
        );
    }

    return (
        <div className="mb-4">
            <div className="d-flex justify-content-between align-items-center mb-3">
                <h3>Resource Allocations</h3>
                <div>
                    {hasEditingAllocations && (
                        <Button
                            color="success"
                            size="sm"
                            onClick={handleUpdate}
                            disabled={bulkUpdateAllocation.isPending}
                            className="me-2"
                        >
                            <Save size={14} className="me-1" />
                            Update Allocations
                        </Button>
                    )}
                </div>
            </div>

            {/* Display update errors */}
            <ErrorDisplay
                error={bulkUpdateAllocation.error}
                title="Failed to update allocations"
                onDismiss={() => bulkUpdateAllocation.reset()}
                className="mb-3"
            />

            <ErrorDisplay
                error={allocationsError}
                title="Failed to load allocations"
                className="mb-3"
            />

            {deleteAllocation.error && (
                <ErrorDisplay
                    error={deleteAllocation.error}
                    title="Failed to delete allocation"
                    onDismiss={() => deleteAllocation.reset()}
                    className="mb-3"
                />
            )}

            {weekAllocations.length === 0 ? (
                <p className="text-muted">No weeks available for allocation</p>
            ) : (
                <div className="border rounded">
                    {weekAllocations.map(week => (
                        <div
                            key={week.globalWeekNumber}
                            className="border-bottom"
                        >
                            <div
                                className="d-flex justify-content-between align-items-center p-3 bg-light cursor-pointer"
                                onClick={() =>
                                    toggleWeekExpansion(week.globalWeekNumber)
                                }
                                style={{cursor: 'pointer'}}
                            >
                                <div className="d-flex align-items-center">
                                    {week.isExpanded ? (
                                        <ChevronDown
                                            size={16}
                                            className="me-2"
                                        />
                                    ) : (
                                        <ChevronRight
                                            size={16}
                                            className="me-2"
                                        />
                                    )}
                                    <Calendar
                                        size={16}
                                        className="me-2 text-muted"
                                    />
                                    <div>
                                        <WeekDisplay weekInfo={week.weekInfo} />
                                    </div>
                                </div>
                                <div className="d-flex align-items-center">
                                    <span className="badge bg-secondary me-2">
                                        {week.allocations.length} allocation
                                        {week.allocations.length !== 1
                                            ? 's'
                                            : ''}
                                    </span>
                                    {canCurrentUserEdit && (
                                        <Button
                                            color="primary"
                                            outline
                                            size="sm"
                                            onClick={e => {
                                                e.stopPropagation();
                                                addNewAllocation(
                                                    week.globalWeekNumber
                                                );
                                            }}
                                        >
                                            <Plus size={14} className="me-1" />
                                            Add Resource
                                        </Button>
                                    )}
                                </div>
                            </div>

                            {week.isExpanded && (
                                <div className="p-3">
                                    {week.allocations.length === 0 ? (
                                        <Alert color="info" className="mb-0">
                                            <small>
                                                No resource allocations for this
                                                week
                                            </small>
                                        </Alert>
                                    ) : (
                                        <div className="table-responsive">
                                            <table className="table table-sm mb-0">
                                                <thead>
                                                    <tr>
                                                        <th>Resource</th>
                                                        <th>Description</th>
                                                        <th>Hours</th>
                                                        <th>Type</th>
                                                        {canCurrentUserEdit && (
                                                            <th>Actions</th>
                                                        )}
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {week.allocations.map(
                                                        allocation => (
                                                            <React.Fragment
                                                                key={
                                                                    allocation.id
                                                                }
                                                            >
                                                                {/* Readonly row */}
                                                                <tr>
                                                                    <td>
                                                                        <div className="d-flex align-items-center">
                                                                            {allocation.person ? (
                                                                                <>
                                                                                    <User
                                                                                        size={
                                                                                            12
                                                                                        }
                                                                                        className="me-1 text-primary"
                                                                                    />
                                                                                    <span>
                                                                                        {
                                                                                            allocation
                                                                                                .person
                                                                                                .firstName
                                                                                        }{' '}
                                                                                        {
                                                                                            allocation
                                                                                                .person
                                                                                                .lastName
                                                                                        }
                                                                                    </span>
                                                                                </>
                                                                            ) : allocation.team ? (
                                                                                <>
                                                                                    <Users
                                                                                        size={
                                                                                            12
                                                                                        }
                                                                                        className="me-1 text-info"
                                                                                    />
                                                                                    <span>
                                                                                        {
                                                                                            allocation
                                                                                                .team
                                                                                                .name
                                                                                        }
                                                                                    </span>
                                                                                </>
                                                                            ) : (
                                                                                <span className="text-muted">
                                                                                    No
                                                                                    resource
                                                                                    assigned
                                                                                </span>
                                                                            )}
                                                                        </div>
                                                                    </td>
                                                                    <td>
                                                                        {allocation.description || (
                                                                            <span className="text-muted">
                                                                                No
                                                                                description
                                                                            </span>
                                                                        )}
                                                                    </td>
                                                                    <td>
                                                                        <div>
                                                                            <div>
                                                                                {(
                                                                                    allocation.hoursBAU +
                                                                                    allocation.hoursNonBAU
                                                                                ).toFixed(
                                                                                    1
                                                                                )}
                                                                                h
                                                                            </div>
                                                                            {allocation.hoursBAU >
                                                                                0 &&
                                                                                allocation.hoursNonBAU >
                                                                                    0 && (
                                                                                    <div className="small text-muted">
                                                                                        {
                                                                                            allocation.hoursBAU
                                                                                        }
                                                                                        h
                                                                                        BAU
                                                                                        +{' '}
                                                                                        {
                                                                                            allocation.hoursNonBAU
                                                                                        }
                                                                                        h
                                                                                        NonBAU
                                                                                    </div>
                                                                                )}
                                                                        </div>
                                                                    </td>
                                                                    <td>
                                                                        <div>
                                                                            <div className="small">
                                                                                {
                                                                                    AllocationType[
                                                                                        allocation
                                                                                            .allocationType
                                                                                    ]
                                                                                }
                                                                            </div>
                                                                        </div>
                                                                    </td>
                                                                    {canCurrentUserEdit && (
                                                                        <td>
                                                                            <div className="btn-group-inline">
                                                                                <Button
                                                                                    color="link"
                                                                                    size="sm"
                                                                                    className="p-0 me-2"
                                                                                    onClick={() =>
                                                                                        toggleAllocationEdit(
                                                                                            week.globalWeekNumber,
                                                                                            allocation.id
                                                                                        )
                                                                                    }
                                                                                    title={
                                                                                        allocation.isEditing
                                                                                            ? 'Cancel editing'
                                                                                            : 'Edit allocation'
                                                                                    }
                                                                                >
                                                                                    {allocation.isEditing ? (
                                                                                        <X
                                                                                            size={
                                                                                                14
                                                                                            }
                                                                                        />
                                                                                    ) : (
                                                                                        <Edit
                                                                                            size={
                                                                                                14
                                                                                            }
                                                                                        />
                                                                                    )}
                                                                                </Button>
                                                                                {!allocation.isNew && (
                                                                                    <Button
                                                                                        color="link"
                                                                                        size="sm"
                                                                                        className="p-0 text-danger"
                                                                                        onClick={() =>
                                                                                            handleDeleteAllocation(
                                                                                                allocation.id
                                                                                            )
                                                                                        }
                                                                                        disabled={
                                                                                            deleteAllocation.isPending
                                                                                        }
                                                                                        title="Delete allocation"
                                                                                    >
                                                                                        <Trash2
                                                                                            size={
                                                                                                14
                                                                                            }
                                                                                        />
                                                                                    </Button>
                                                                                )}
                                                                            </div>
                                                                        </td>
                                                                    )}
                                                                </tr>
                                                                {/* Edit form row */}
                                                                {allocation.isEditing && (
                                                                    <tr>
                                                                        <td
                                                                            colSpan={
                                                                                canCurrentUserEdit
                                                                                    ? 6
                                                                                    : 5
                                                                            }
                                                                            className="p-0"
                                                                        >
                                                                            <AllocationEditForm
                                                                                allocation={
                                                                                    allocation
                                                                                }
                                                                                project={
                                                                                    project
                                                                                }
                                                                                teamsData={
                                                                                    teamsData
                                                                                }
                                                                                bulkUpdateAllocation={
                                                                                    bulkUpdateAllocation
                                                                                }
                                                                                onUpdateField={
                                                                                    updateAllocationField
                                                                                }
                                                                                onDelete={
                                                                                    handleDeleteAllocation
                                                                                }
                                                                                deleteAllocation={
                                                                                    deleteAllocation
                                                                                }
                                                                            />
                                                                        </td>
                                                                    </tr>
                                                                )}
                                                            </React.Fragment>
                                                        )
                                                    )}
                                                </tbody>
                                            </table>
                                        </div>
                                    )}
                                </div>
                            )}
                        </div>
                    ))}
                </div>
            )}

            {/* Reason for Change Modal */}
            <ReasonForChangeModal
                isOpen={isReasonModalOpen}
                onClose={() => {
                    setIsReasonModalOpen(false);
                    setPendingUpdates([]);
                }}
                onSubmit={submitUpdates}
                description={`update ${pendingUpdates.length} allocation${pendingUpdates.length > 1 ? 's' : ''}`}
                isPending={bulkUpdateAllocation.isPending}
                projectStatus={project?.status}
            />

            {/* Delete Allocation Confirmation Modal */}
            <ReasonForChangeModal
                isOpen={isDeleteModalOpen}
                onClose={() => {
                    setIsDeleteModalOpen(false);
                    setAllocationToDelete(null);
                }}
                onSubmit={confirmDeleteAllocation}
                description="delete this allocation"
                isPending={deleteAllocation.isPending}
                projectStatus={project?.status}
            />
        </div>
    );
};

export default AllocationSection;
