import React, {useState} from 'react';
import {
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Button,
    Form,
    FormGroup,
    Input,
    Alert
} from 'reactstrap';
import {ProjectStatus} from '../../../types';

export interface ReasonForChangeModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSubmit: (reason: string) => void;
    description: string;
    isPending?: boolean;
    projectStatus?: ProjectStatus;
}

const ReasonForChangeModal: React.FC<ReasonForChangeModalProps> = ({
    isOpen,
    onClose,
    onSubmit,
    description,
    isPending = false,
    projectStatus
}) => {
    const [reason, setReason] = useState('');
    const [error, setError] = useState<string | null>(null);

    const isReasonRequired = projectStatus === ProjectStatus.Published;

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        setError(null);

        const trimmedReason = reason.trim();

        // Only validate reason if it's required (for published projects)
        if (isReasonRequired) {
            if (
                !trimmedReason ||
                trimmedReason.length < 5 ||
                /^[.,]+$/.test(trimmedReason)
            ) {
                setError('Please provide a meaningful reason for this change');
                return;
            }
        }

        onSubmit(trimmedReason);
        setReason('');
        onClose();
    };

    const handleClose = () => {
        setReason('');
        setError(null);
        onClose();
    };

    return (
        <Modal isOpen={isOpen} toggle={handleClose}>
            <Form onSubmit={handleSubmit}>
                <ModalHeader toggle={handleClose}>
                    {isReasonRequired
                        ? 'Reason for Change Required'
                        : 'Confirm Action'}
                </ModalHeader>
                <ModalBody>
                    <p>
                        You are about to: <strong>{description}</strong>
                    </p>
                    <p>
                        {isReasonRequired
                            ? 'Please provide a reason for this change:'
                            : 'You may optionally provide a reason for this change:'}
                    </p>

                    {error && <Alert color="danger">{error}</Alert>}

                    <FormGroup>
                        <Input
                            id="changeReason"
                            type="textarea"
                            value={reason}
                            onChange={e => setReason(e.target.value)}
                            rows={3}
                            disabled={isPending}
                            required={isReasonRequired}
                        />
                    </FormGroup>
                </ModalBody>
                <ModalFooter>
                    <Button
                        color="secondary"
                        onClick={handleClose}
                        disabled={isPending}
                    >
                        Cancel
                    </Button>
                    <Button color="primary" type="submit" disabled={isPending}>
                        {isPending ? 'Saving...' : 'Submit'}
                    </Button>
                </ModalFooter>
            </Form>
        </Modal>
    );
};

export default ReasonForChangeModal;
