import {
    useMutation,
    useQueryClient,
    UseMutationResult
} from '@tanstack/react-query';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';
import {PhaseType, EmptyResult} from '../../types.ts';

export interface BulkPhaseUpdateItem {
    id?: string; // undefined for new phases
    name?: string;
    description?: string;
    startGlobalWeekNumber?: number;
    durationInWeeks?: number; // Duration in weeks
    type?: PhaseType;
    colour?: string;
    index: number; // Track the original index for error mapping
}

export interface BulkPhaseUpdateCommand {
    projectId: string;
    phases: BulkPhaseUpdateItem[];
    changeReason: string;
}

export const useBulkPhaseUpdate = (): UseMutationResult<
    EmptyResult,
    Error | ProblemDetailsError,
    BulkPhaseUpdateCommand
> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();
    const queryClient = useQueryClient();

    return useMutation({
        mutationFn: async (command: BulkPhaseUpdateCommand) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/projects/phases/bulk-update`,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(command)
                }
            );
            return handleResponse<EmptyResult>(response);
        },
        onSuccess: (_, variables) => {
            queryClient.invalidateQueries({
                queryKey: ['projects', variables.projectId]
            });
        }
    });
};
