import React from 'react';
import ProblemDetailsError from '../../api/ProblemDetailsError';
import {ProblemDetails} from '../../types';
import isProblemDetails from '../../lib/isProblemDetails';

interface FieldErrorProps {
    path: string;
    error: unknown;
    className?: string;
}

/**
 * Component that displays field-specific error messages.
 * Designed to work with ProblemDetails errors from the API.
 * @param path - The dot-notation path to the field (e.g., "phases.0.endDate")
 * @param error - The error object from the API (ProblemDetailsError or ProblemDetails)
 * @param className - Optional CSS class name for styling
 */
const FieldError: React.FC<FieldErrorProps> = ({
    path,
    error,
    className = ''
}) => {
    if (!error) {
        return null;
    }

    let problemDetails: ProblemDetails | null = null;

    // Extract ProblemDetails from different error types
    if (error instanceof ProblemDetailsError) {
        problemDetails = error.problemDetails;
    } else if (isProblemDetails(error)) {
        problemDetails = error;
    } else {
        return null;
    }

    if (!problemDetails.errors) {
        return null;
    }

    // Handle array format (with key property)
    if (Array.isArray(problemDetails.errors)) {
        const fieldError = problemDetails.errors.find(
            error => error.key === path
        );
        if (fieldError) {
            return (
                <div className={`text-danger small mt-1 ${className}`}>
                    {fieldError.message}
                </div>
            );
        }
        return null;
    }

    // Handle object format (keyed errors like "allocations[0].hours")
    const errorsAsObject = problemDetails.errors as Record<string, any>;
    const fieldErrors = errorsAsObject[path];
    if (fieldErrors) {
        const errorMessage = Array.isArray(fieldErrors)
            ? fieldErrors.join(', ')
            : String(fieldErrors);

        return (
            <div className={`text-danger small mt-1 ${className}`}>
                {errorMessage}
            </div>
        );
    }

    return null;
};

export default FieldError;
