import React from 'react';
import {Button, Col, Row} from 'reactstrap';
import {
    Calendar,
    Clock,
    Eye,
    Globe,
    Mail,
    Phone,
    Target,
    User,
    Users
} from 'lucide-react';
import {formatDate} from '../../../lib/dateUtils';
import {breakToWords} from '../../../lib/convertCase';
import PhasesSection from './PhasesSection.tsx';
import AllocationSection from './AllocationSection.tsx';
import DisplayTag from '../../utility/DisplayTag.tsx';
import {useAuthStateManager} from '../../../hooks/useAuthStateManager';
import {
    ProjectModel,
    ProjectStatus,
    ProjectVisibility,
    EngagementContext
} from '../../../types.ts';

interface ProjectDisplayProps {
    project: ProjectModel;
    onEdit: (project: ProjectModel) => void;
}

const ProjectDisplay: React.FC<ProjectDisplayProps> = ({project, onEdit}) => {
    const {personId} = useAuthStateManager();

    // Check if current user can edit (using fetched data, not edited data)
    const canCurrentUserEdit =
        personId &&
        project.people.some(
            p => p.personId === personId && p.canEdit
        );

    return (
        <div className="p-4">
            {/* Project Header */}
            <div className="d-flex justify-content-between align-items-start mb-4">
                <div>
                    <h2>{project.name}</h2>
                    {project.description && (
                        <p className="text-muted mb-0">{project.description}</p>
                    )}
                </div>
                <div>
                    {canCurrentUserEdit && (
                        <Button
                            color="primary"
                            outline
                            onClick={() => onEdit(project)}
                            className="me-2"
                        >
                            Edit
                        </Button>
                    )}
                </div>
            </div>

            {/* Tags Section */}
            {project.tags && project.tags.length > 0 && (
                <div className="mb-4">
                    <div className="d-flex flex-wrap gap-2">
                        {project.tags.map(tag => (
                            <DisplayTag key={tag.id || tag.name} tag={tag} />
                        ))}
                    </div>
                </div>
            )}

            {/* Project Details - Compact Cards */}
            <Row className="mb-4">
                <Col md={3}>
                    <div className="d-flex align-items-center mb-2">
                        <Target size={16} className="text-muted me-2" />
                        <div>
                            <small className="text-muted d-block">Status</small>
                            <span className="fw-medium">
                                {ProjectStatus[project.status]}
                            </span>
                        </div>
                    </div>
                </Col>
                <Col md={3}>
                    <div className="d-flex align-items-center mb-2">
                        <Calendar size={16} className="text-muted me-2" />
                        <div>
                            <small className="text-muted d-block">Start</small>
                            <span className="fw-medium">
                                {formatDate(project.startOn)}
                            </span>
                        </div>
                    </div>
                </Col>
                <Col md={3}>
                    <div className="d-flex align-items-center mb-2">
                        <Calendar size={16} className="text-muted me-2" />
                        <div>
                            <small className="text-muted d-block">End</small>
                            <span className="fw-medium">
                                {formatDate(
                                    new Date(
                                        new Date(project.startOn).getTime() +
                                            (project.durationInWeeks * 7 - 1) *
                                                24 *
                                                60 *
                                                60 *
                                                1000
                                    ).toISOString()
                                )}
                            </span>
                        </div>
                    </div>
                </Col>
                <Col md={3}>
                    <div className="d-flex align-items-center mb-2">
                        <Globe size={16} className="text-muted me-2" />
                        <div>
                            <small className="text-muted d-block">
                                Timezone
                            </small>
                            <span className="fw-medium">
                                {project.timeZone || 'Not set'}
                            </span>
                        </div>
                    </div>
                </Col>
            </Row>

            {/* Secondary Details Row */}
            <Row className="mb-4">
                <Col md={3}>
                    <div className="d-flex align-items-center mb-2">
                        <Eye size={16} className="text-muted me-2" />
                        <div>
                            <small className="text-muted d-block">
                                Visibility
                            </small>
                            <span className="fw-medium">
                                {breakToWords(
                                    ProjectVisibility[project.visibility]
                                )}
                            </span>
                        </div>
                    </div>
                </Col>
                <Col md={3}>
                    <div className="d-flex align-items-center mb-2">
                        <Clock size={16} className="text-muted me-2" />
                        <div>
                            <small className="text-muted d-block">
                                Duration
                            </small>
                            <span className="fw-medium">
                                {project.durationInWeeks} week
                                {project.durationInWeeks !== 1 ? 's' : ''}
                            </span>
                        </div>
                    </div>
                </Col>
                <Col md={3}>
                    <div className="d-flex align-items-center mb-2">
                        <Target size={16} className="text-muted me-2" />
                        <div>
                            <small className="text-muted d-block">
                                Engagement Context
                            </small>
                            <span className="fw-medium">
                                {project.engagementContext === EngagementContext.BAU ? 'Business As Usual' : 'Non-Business As Usual'}
                            </span>
                            <div className="small text-muted mt-1">
                                {project.engagementContext === EngagementContext.BAU
                                    ? 'Routine operational work or standard business activities'
                                    : 'Change initiatives, strategic projects, or transformation efforts that are outside normal operations'
                                }
                            </div>
                        </div>
                    </div>
                </Col>
            </Row>

            {/* People Section - Compact Cards */}
            {project.people.length > 0 && (
                <div className="mb-4">
                    <div className="d-flex align-items-center mb-3">
                        <Users size={20} className="text-muted me-2" />
                        <h3 className="mb-0">People</h3>
                        <span className="badge bg-secondary ms-2">
                            {project.people.length}
                        </span>
                    </div>
                    <Row>
                        {project.people.map(
                            (person: ProjectModel['people'][0]) => (
                                <Col
                                    md={6}
                                    lg={4}
                                    key={person.id}
                                    className="mb-3"
                                >
                                    <div className="border rounded p-3 h-100">
                                        <div className="d-flex align-items-start">
                                            <div className="bg-light rounded-circle p-2 me-3 flex-shrink-0">
                                                <User
                                                    size={16}
                                                    className="text-muted"
                                                />
                                            </div>
                                            <div className="flex-grow-1 min-width-0">
                                                <div className="fw-medium text-truncate">
                                                    {`${person.person.firstName} ${person.person.lastName}`}
                                                </div>
                                                <div className="small text-muted mb-1">
                                                    {person.projectRole}
                                                    {person.canEdit && ' (Editor)'}
                                                </div>
                                                <div className="d-flex align-items-center small text-muted">
                                                    <Mail
                                                        size={12}
                                                        className="me-1 flex-shrink-0"
                                                    />
                                                    <a
                                                        href={`mailto:${person.person.email}`}
                                                        className="text-truncate text-primary"
                                                        style={{
                                                            cursor: 'pointer',
                                                            textDecoration:
                                                                'none',
                                                            transition:
                                                                'all 0.2s ease'
                                                        }}
                                                        onMouseEnter={e => {
                                                            const target =
                                                                e.target as HTMLElement;
                                                            target.style.textDecoration =
                                                                'underline';
                                                        }}
                                                        onMouseLeave={e => {
                                                            const target =
                                                                e.target as HTMLElement;
                                                            target.style.textDecoration =
                                                                'none';
                                                        }}
                                                        title={`Send email to ${person.person.email}`}
                                                    >
                                                        {person.person.email}
                                                    </a>
                                                </div>
                                                {person.person.phoneNumber && (
                                                    <div className="d-flex align-items-center small text-muted">
                                                        <Phone
                                                            size={12}
                                                            className="me-1 flex-shrink-0"
                                                        />
                                                        <a
                                                            href={`tel:${person.person.phoneNumber}`}
                                                            className="text-truncate text-primary"
                                                            style={{
                                                                cursor: 'pointer',
                                                                textDecoration:
                                                                    'none',
                                                                transition:
                                                                    'all 0.2s ease'
                                                            }}
                                                            onMouseEnter={e => {
                                                                const target =
                                                                    e.target as HTMLElement;
                                                                target.style.textDecoration =
                                                                    'underline';
                                                            }}
                                                            onMouseLeave={e => {
                                                                const target =
                                                                    e.target as HTMLElement;
                                                                target.style.textDecoration =
                                                                    'none';
                                                            }}
                                                            title={`Call ${person.person.phoneNumber}`}
                                                        >
                                                            {
                                                                person.person
                                                                    .phoneNumber
                                                            }
                                                        </a>
                                                    </div>
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                </Col>
                            )
                        )}
                    </Row>
                </div>
            )}

            {/* Phases Section */}
            <PhasesSection
                project={project}
                canCurrentUserEdit={!!canCurrentUserEdit}
            />

            {/* Resource Allocations Section */}
            <AllocationSection
                project={project}
                canCurrentUserEdit={!!canCurrentUserEdit}
            />
        </div>
    );
};

export default ProjectDisplay;
