import React, {useState, useMemo} from 'react';
import {Spinner} from 'reactstrap';
import ErrorDisplay from '../../utility/ErrorDisplay';
import emptyGuid from '../../../lib/emptyGuid';
import {useAuthStateManager} from '../../../hooks/useAuthStateManager.tsx';
import ProjectEdit from './ProjectEdit.tsx';
import ProjectDisplay from './ProjectDisplay.tsx';
import {
    ProjectModel,
    ProjectStatus,
    ProjectVisibility,
    EngagementContext,
} from '../../../types.ts';
import {useGetProjectById} from '../../../api/projects/useGetProjectById.ts';
import {getWeekInfo} from '../../../lib/scheduling.ts';

const getDefaultProjectData = (
    customerId: string | null | undefined,
    personId: string | null | undefined
): ProjectModel => {
    const {mondayOfWeek: defaultStartOn, globalWeekNumber} = getWeekInfo(new Date());
    const defaultStartOnDateString = defaultStartOn.toISOString().split('T')[0];
    
    // Calculate end date based on 1 week duration
    const endDate = new Date(defaultStartOn);
    endDate.setDate(endDate.getDate() + 6); // End on Sunday of the same week

    return {
        id: null,
        name: '',
        description: '',
        timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
        startOn: defaultStartOnDateString,
        durationInWeeks: 1,
        startGlobalWeekNumber: globalWeekNumber,
        endGlobalWeekNumber: globalWeekNumber, // Same week for 1 week duration
        endOn: endDate.toISOString(),
        status: ProjectStatus.Draft,
        visibility: ProjectVisibility.NonDisclosed,
        engagementContext: EngagementContext.BAU,
        customerId: customerId ?? emptyGuid,
        phases: [],
        people: personId
            ? [
                  {
                      id: 'temp-owner',
                      projectId: '',
                      personId: personId,
                      projectRole: 'Project Owner',
                      canEdit: true,
                      canEditResources: true,
                      person: {
                          id: personId,
                          firstName: '',
                          lastName: '',
                          email: '',
                          phoneNumber: null
                      }
                  }
              ]
            : [],
        tags: []
    };
};

interface ProjectDetailsProps {
    projectId: string | null;
}

const ProjectDetails: React.FC<ProjectDetailsProps> = ({projectId}) => {
    const {customerId, personId} = useAuthStateManager();

    const selectedProjectId = projectId === '_' ? emptyGuid : projectId;

    // State for edit mode
    const [isEditing, setIsEditing] = useState(false);

    // Fetch project (unless new)
    const {
        data: selectedProject,
        isLoading: isLoadingDetails,
        error: detailsError
    } = useGetProjectById(selectedProjectId);

    // If new project, create default model
    const isNewProject = selectedProjectId === emptyGuid;
    const newProjectModel = useMemo(
        () =>
            isNewProject
                ? getDefaultProjectData(customerId, personId)
                : undefined,
        [isNewProject, customerId, personId]
    );

    // Phase edit navigation handlers


    // Edit handlers
    const handleEditProject = () => {
        setIsEditing(true);
    };

    const handleEditSave = () => {
        setIsEditing(false);
    };

    const handleEditCancel = () => {
        setIsEditing(false);
    };

    // If new project, always in edit mode
    const shouldEdit = isNewProject || isEditing;

    // Project to display/edit
    const projectToUse = isNewProject ? newProjectModel : selectedProject;

    return (
        <>
            {isLoadingDetails && !isNewProject ? (
                <div className="text-center p-4">
                    <Spinner size="sm" /> Loading details...
                </div>
            ) : detailsError ? (
                <ErrorDisplay error={detailsError} />
            ) : !projectToUse ? (
                <div className="text-center text-muted p-5">
                    Select a project from the left to view details
                </div>
            ) : shouldEdit ? (
                <ProjectEdit
                    project={projectToUse}
                    onSave={handleEditSave}
                    onCancel={handleEditCancel}
                />
            ) : (
                <ProjectDisplay
                    project={projectToUse}
                    onEdit={handleEditProject}
                />
            )}
        </>
    );
};

export default ProjectDetails;
