import {
    useMutation,
    useQueryClient,
    UseMutationResult
} from '@tanstack/react-query';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';
import {AllocationType, EmptyResult} from '../../types.ts';

export interface ProjectAllocationUpdateItem {
    id?: string; // undefined for new allocations
    personId?: string;
    teamId?: string;
    description?: string;
    globalWeekNumber?: number;
    hoursBAU?: number;
    hoursNonBAU?: number;
    allocationType?: AllocationType;
    index: number; // Track the original index for error mapping
}

export interface BulkAllocationUpdateCommand {
    projectId: string;
    allocations: ProjectAllocationUpdateItem[];
    changeReason: string;
}

export const useBulkUpdateAllocation = (): UseMutationResult<
    EmptyResult,
    Error | ProblemDetailsError,
    BulkAllocationUpdateCommand
> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();
    const queryClient = useQueryClient();

    return useMutation({
        mutationFn: async (command: BulkAllocationUpdateCommand) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/projects/${command.projectId}/allocations/bulk-update`,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(command)
                }
            );
            return handleResponse<EmptyResult>(response);
        },
        onSuccess: (_, variables) => {
            queryClient.invalidateQueries({
                queryKey: ['project-allocations', variables.projectId]
            });
            queryClient.invalidateQueries({
                queryKey: ['project-allocations']
            });
        }
    });
};
