import React, {useMemo, useCallback} from 'react';
import {FormGroup, Label, Input, InputGroup, FormText} from 'reactstrap';
import {
    getWeekInfo,
    getMondayFromGlobalWeek,
    addDays,
    getMondayOfWeek,
    getISOWeekYear
} from '../../lib/scheduling';
import type {WeekInfo} from '../../lib/scheduling';
import {formatDate} from '../../lib/dateUtils.ts';

interface WeekSelectorProps {
    value?: number | null; // Allow null and undefined globalWeekNumber
    onChange: (globalWeekNumber: number, weekInfo: WeekInfo) => void;
    label?: string;
    // No min/max props needed here, parent should enforce this before passing value
    className?: string;
    disabled?: boolean;
}

const WeekSelector: React.FC<WeekSelectorProps> = ({
    value,
    onChange,
    label = 'Week',
    className = '',
    disabled = false
}) => {
    // A safe utility to get week info from a value, always falling back to a sane default.
    const getSafeInfo = useCallback((weekNumber?: number | null): WeekInfo => {
        try {
            // This now correctly handles null/undefined `value`
            if (weekNumber && weekNumber > 0) {
                return getWeekInfo(getMondayFromGlobalWeek(weekNumber));
            }
            // If value is missing or invalid, default to the current week.
            return getWeekInfo(new Date());
        } catch (e) {
            // If the current week is somehow invalid (e.g., before epoch), default to week 1.
            return getWeekInfo(getMondayFromGlobalWeek(1));
        }
    }, []);

    // --- DERIVED STATE ---
    // All displayed information is derived directly from the `value` prop.
    // There is no internal state for the selected week.

    const weekInfo = useMemo(() => getSafeInfo(value), [value, getSafeInfo]);
    const displayYear = useMemo(
        () => getISOWeekYear(weekInfo.mondayOfWeek),
        [weekInfo]
    );

    // Memoized function to generate the list of weeks for the displayed year.
    const getWeeksForYear = useCallback((year: number): WeekInfo[] => {
        if (year < 2025) return [];
        const weeks: WeekInfo[] = [];
        try {
            let currentMonday = getMondayOfWeek(new Date(year, 0, 4));
            if (getISOWeekYear(currentMonday) < year) {
                currentMonday = addDays(currentMonday, 7);
            }

            for (let i = 0; i < 54; i++) {
                const isoYear = getISOWeekYear(currentMonday);
                if (isoYear === year) {
                    weeks.push(getWeekInfo(currentMonday));
                } else if (isoYear > year) {
                    break;
                }
                currentMonday = addDays(currentMonday, 7);
            }
        } catch (error) {
            console.error(`Error generating weeks for year ${year}:`, error);
        }
        return weeks;
    }, []);

    const generatedWeeks = useMemo(
        () => getWeeksForYear(displayYear),
        [displayYear, getWeeksForYear]
    );

    // --- EVENT HANDLERS ---
    // These functions ONLY calculate the desired new week and call `onChange`.

    const handleYearChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const year = parseInt(e.target.value, 10);
        if (!isNaN(year) && year >= 2025) {
            const weeksOfNewYear = getWeeksForYear(year);
            if (weeksOfNewYear.length > 0) {
                const firstWeekInfo = weeksOfNewYear[0];
                onChange(firstWeekInfo.globalWeekNumber, firstWeekInfo);
            }
        }
    };

    const handleWeekChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const newGlobalWeek = parseInt(e.target.value, 10);
        if (!isNaN(newGlobalWeek)) {
            const newWeekInfo = getSafeInfo(newGlobalWeek);
            onChange(newGlobalWeek, newWeekInfo);
        }
    };

    // The component now simply renders based on the props it receives.
    return (
        <FormGroup className={className}>
            {label && (
                <Label for="week-selector-year" className="mb-1">
                    {label}
                </Label>
            )}
            <InputGroup size="sm">
                <Input
                    id="week-selector-year"
                    type="number"
                    value={displayYear}
                    onChange={handleYearChange}
                    disabled={disabled}
                    min="2025"
                    style={{flex: '0 0 90px'}}
                    aria-label="Year"
                />
                <Input
                    id="week-selector-week"
                    type="select"
                    // The select's value is the `value` prop, or an empty string if it's null/undefined.
                    value={value || ''}
                    onChange={handleWeekChange}
                    disabled={disabled || generatedWeeks.length === 0}
                    aria-label="Week"
                >
                    {generatedWeeks.map(info => (
                        <option
                            key={info.globalWeekNumber}
                            value={info.globalWeekNumber}
                        >
                            {`Week ${info.weekOfYear} (${formatDate(info.mondayOfWeek)})`}
                        </option>
                    ))}
                </Input>
            </InputGroup>
            {weekInfo && (
                <FormText>Mon, {formatDate(weekInfo.mondayOfWeek)}</FormText>
            )}
        </FormGroup>
    );
};

export default WeekSelector;
