import React, {useEffect, useState} from 'react';
import {Button, FormGroup, Input, FormText} from 'reactstrap';
import {
    ChevronDown,
    ChevronRight,
    Edit,
    Plus,
    Save,
    Trash2,
    X
} from 'lucide-react';
import Select from 'react-select';
import {formatDate} from '../../../lib/dateUtils';
import {getNextPhaseColor} from '../../../lib/defaultPhaseColors';
import WeekSelector from '../../utility/WeekSelector';
import {
    getWeekInfo,
    getMondayFromGlobalWeek,
    addDays
} from '../../../lib/scheduling';
import ReasonForChangeModal from './ReasonForChangeModal.tsx';
import ErrorDisplay from '../../utility/ErrorDisplay.tsx';
import FieldError from '../../utility/FieldError.tsx';
import {
    BulkPhaseUpdateItem,
    useBulkPhaseUpdate
} from '../../../api/projects/useBulkPhaseMutations.ts';
import {useDeletePhase} from '../../../api/projects/useDeletePhase.ts';
import {ProjectModel, PhaseModel, PhaseType} from '../../../types.ts';

interface EditablePhase extends PhaseModel {
    isNew?: boolean;
    isEditing?: boolean;
    hasChanges?: boolean;
    originalData?: Partial<PhaseModel>;
    index: number; // Track the index for error mapping
}

interface PhasesSectionProps {
    project: ProjectModel;
    canCurrentUserEdit: boolean;
}

const PhasesSection: React.FC<PhasesSectionProps> = ({
    project,
    canCurrentUserEdit
}) => {
    const [expandedPhases, setExpandedPhases] = useState<Set<string>>(
        new Set()
    );
    const [editablePhases, setEditablePhases] = useState<EditablePhase[]>([]);
    const [isReasonModalOpen, setIsReasonModalOpen] = useState(false);
    const [pendingUpdates, setPendingUpdates] = useState<BulkPhaseUpdateItem[]>(
        []
    );
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
    const [phaseToDelete, setPhaseToDelete] = useState<string | null>(null);

    const bulkPhaseUpdate = useBulkPhaseUpdate();
    const deletePhase = useDeletePhase();

    // Initialize editable phases from project phases
    useEffect(() => {
        const phases: EditablePhase[] = project.phases.map((phase, index) => ({
            ...phase,
            isNew: false,
            isEditing: false,
            hasChanges: false,
            originalData: {...phase},
            index
        }));
        setEditablePhases(phases);
    }, [project.phases]);

    const togglePhaseExpansion = (phaseId: string) => {
        setExpandedPhases(prev => {
            const newSet = new Set(prev);
            if (newSet.has(phaseId)) {
                newSet.delete(phaseId);
            } else {
                newSet.add(phaseId);
            }
            return newSet;
        });
    };

    const addNewPhase = () => {
        // Default to project start week for new phases
        const defaultStartWeek = project.startGlobalWeekNumber;
        const defaultStartMonday = getMondayFromGlobalWeek(defaultStartWeek);
        const defaultEndWeek = defaultStartWeek; // 1 week duration by default
        const defaultEndMonday = getMondayFromGlobalWeek(defaultEndWeek);
        const defaultEndSunday = addDays(defaultEndMonday, 6);

        const newPhase: EditablePhase = {
            id: `temp-${Date.now()}`,
            projectId: project.id!,
            name: '',
            description: '',
            startGlobalWeekNumber: defaultStartWeek,
            startOn: defaultStartMonday.toISOString(),
            endGlobalWeekNumber: defaultEndWeek,
            endOn: defaultEndSunday.toISOString(),
            durationInWeeks: 1,
            type: PhaseType.Delivery,
            colour: getNextPhaseColor(editablePhases), // Color based on position
            isNew: true,
            isEditing: true,
            hasChanges: true,
            originalData: {},
            index: editablePhases.length // New phase gets the next index
        };
        setEditablePhases(prev => [...prev, newPhase]);
    };

    const togglePhaseEdit = (phaseId: string) => {
        setEditablePhases(
            prev =>
                prev
                    .map(phase => {
                        if (phase.id === phaseId) {
                            if (phase.isEditing) {
                                // Cancel editing - revert to original data
                                if (phase.isNew) {
                                    return null; // Remove new phases when canceling
                                }
                                return {
                                    ...phase.originalData!,
                                    isEditing: false,
                                    hasChanges: false,
                                    originalData: phase.originalData
                                } as EditablePhase;
                            } else {
                                // Start editing
                                return {
                                    ...phase,
                                    isEditing: true,
                                    originalData: {...phase}
                                };
                            }
                        }
                        return phase;
                    })
                    .filter(Boolean) as EditablePhase[]
        );
    };

    const updatePhaseField = (
        phaseId: string,
        field: keyof PhaseModel,
        value: any
    ) => {
        setEditablePhases(prev =>
            prev.map(phase => {
                if (phase.id === phaseId) {
                    const updatedPhase = {
                        ...phase,
                        [field]: value,
                        hasChanges: true
                    };
                    return updatedPhase;
                }
                return phase;
            })
        );
    };

    // Helper function to update phase dates when week number or duration changes
    const updatePhaseWeekData = (
        phaseId: string,
        startGlobalWeekNumber: number,
        durationInWeeks: number
    ) => {
        // Ensure phase cannot start before project start
        const constrainedStartWeek = Math.max(
            startGlobalWeekNumber,
            project.startGlobalWeekNumber
        );

        const startMonday = getMondayFromGlobalWeek(constrainedStartWeek);
        const endGlobalWeekNumber = constrainedStartWeek + durationInWeeks - 1;
        const endMonday = getMondayFromGlobalWeek(endGlobalWeekNumber);
        const endSunday = addDays(endMonday, 6);

        setEditablePhases(prev =>
            prev.map(phase => {
                if (phase.id === phaseId) {
                    return {
                        ...phase,
                        startGlobalWeekNumber: constrainedStartWeek,
                        startOn: startMonday.toISOString(),
                        endGlobalWeekNumber,
                        endOn: endSunday.toISOString(),
                        durationInWeeks,
                        hasChanges: true
                    };
                }
                return phase;
            })
        );
    };

    // Helper function to get week info for display
    const getPhaseWeekInfo = (phase: EditablePhase) => {
        try {
            const startWeekInfo = getWeekInfo(
                getMondayFromGlobalWeek(phase.startGlobalWeekNumber)
            );
            const endWeekInfo = getWeekInfo(
                getMondayFromGlobalWeek(phase.endGlobalWeekNumber)
            );
            return {startWeekInfo, endWeekInfo};
        } catch (error) {
            console.error('Error getting phase week info:', error);
            return null;
        }
    };

    // Helper function to get the maximum end week from all phases
    const getMaxPhaseEndWeek = (): number | null => {
        const phaseEndWeeks = editablePhases
            .filter(phase => phase.endGlobalWeekNumber)
            .map(phase => phase.endGlobalWeekNumber);

        if (phaseEndWeeks.length === 0) return null;

        return Math.max(...phaseEndWeeks);
    };

    const getChangedFields = (phase: EditablePhase): BulkPhaseUpdateItem => {
        if (phase.isNew) {
            return {
                name: phase.name,
                description: phase.description,
                startGlobalWeekNumber: phase.startGlobalWeekNumber,
                durationInWeeks: phase.durationInWeeks,
                type: phase.type,
                colour: phase.colour,
                index: phase.index
            };
        }

        const changes: BulkPhaseUpdateItem = {
            id: phase.id,
            index: phase.index
        };

        const original = phase.originalData!;
        if (phase.name !== original.name) changes.name = phase.name;
        if (phase.description !== original.description)
            changes.description = phase.description;
        if (phase.startGlobalWeekNumber !== original.startGlobalWeekNumber)
            changes.startGlobalWeekNumber = phase.startGlobalWeekNumber;
        if (phase.durationInWeeks !== original.durationInWeeks)
            changes.durationInWeeks = phase.durationInWeeks;
        if (phase.type !== original.type) changes.type = phase.type;
        if (phase.colour !== original.colour) changes.colour = phase.colour;

        return changes;
    };

    const handleUpdate = () => {
        const phasesToUpdate = editablePhases.filter(
            phase => phase.isEditing && phase.hasChanges
        );

        if (phasesToUpdate.length === 0) return;

        const updates = phasesToUpdate.map(getChangedFields);
        setPendingUpdates(updates);
        setIsReasonModalOpen(true);
    };

    const submitUpdates = async (reason: string) => {
        if (pendingUpdates.length === 0) {
            return;
        }

        bulkPhaseUpdate.mutate(
            {
                projectId: project.id!,
                phases: pendingUpdates,
                changeReason: reason
            },
            {
                onSuccess: () => {
                    // Reset editing states
                    setEditablePhases(prev =>
                        prev.map(phase => ({
                            ...phase,
                            isEditing: false,
                            hasChanges: false,
                            originalData: {...phase}
                        }))
                    );

                    setPendingUpdates([]);
                    setIsReasonModalOpen(false);
                }
                // onError is handled by displaying bulkPhaseUpdate.error in the UI
            }
        );
    };

    const handleDeletePhase = (phaseId: string) => {
        setPhaseToDelete(phaseId);
        setIsDeleteModalOpen(true);
    };

    const confirmDeletePhase = async (reason: string) => {
        if (phaseToDelete) {
            deletePhase.mutate(
                {
                    phaseId: phaseToDelete,
                    projectId: project.id!,
                    changeReason: reason
                },
                {
                    onSuccess: () => {
                        setIsDeleteModalOpen(false);
                        setPhaseToDelete(null);
                    }
                }
            );
        }
    };

    const hasEditingPhases = editablePhases.some(
        phase => phase.isEditing && phase.hasChanges
    );

    const phaseTypeOptions = [
        {value: PhaseType.Delivery, label: 'Delivery'},
        {value: PhaseType.Delivered, label: 'Delivered'}
    ];

    return (
        <div className="mb-4">
            <div className="d-flex justify-content-between align-items-center mb-3">
                <h3>Phases</h3>
                <div>
                    {hasEditingPhases && (
                        <Button
                            color="success"
                            size="sm"
                            onClick={handleUpdate}
                            disabled={bulkPhaseUpdate.isPending}
                            className="me-2"
                        >
                            <Save size={14} className="me-1" />
                            Update Phases
                        </Button>
                    )}
                    {canCurrentUserEdit && (
                        <Button
                            color="primary"
                            outline
                            size="sm"
                            onClick={addNewPhase}
                            className="me-2"
                        >
                            <Plus size={14} className="me-1" />
                            Add Phase
                        </Button>
                    )}
                </div>
            </div>

            {/* Display update errors */}
            <ErrorDisplay
                error={bulkPhaseUpdate.error}
                title="Failed to update phases"
                onDismiss={() => bulkPhaseUpdate.reset()}
                className="mb-3"
            />

            {deletePhase.error && (
                <ErrorDisplay
                    error={deletePhase.error}
                    title="Failed to delete phase"
                    onDismiss={() => deletePhase.reset()}
                    className="mb-3"
                />
            )}

            {/* Display warning for phases extending beyond project end week */}
            {(() => {
                const maxEndWeek = getMaxPhaseEndWeek();
                const projectEndWeek = project.endGlobalWeekNumber;
                const hasExtendingPhases =
                    maxEndWeek && maxEndWeek > projectEndWeek;

                if (hasExtendingPhases) {
                    try {
                        const projectEndWeekInfo = getWeekInfo(
                            getMondayFromGlobalWeek(projectEndWeek)
                        );
                        const maxEndWeekInfo = getWeekInfo(
                            getMondayFromGlobalWeek(maxEndWeek)
                        );

                        return (
                            <div
                                className="alert alert-warning mb-3"
                                role="alert"
                            >
                                <div className="d-flex align-items-center">
                                    <i className="fas fa-exclamation-triangle me-2"></i>
                                    <div>
                                        <strong>
                                            Project End Week Extension Required
                                        </strong>
                                        <div className="small mt-1">
                                            One or more phases extend beyond the
                                            current project end week (Week{' '}
                                            {projectEndWeekInfo.weekOfYear} of{' '}
                                            {projectEndWeekInfo.year}). The
                                            project will be automatically
                                            extended to Week{' '}
                                            {maxEndWeekInfo.weekOfYear} of{' '}
                                            {maxEndWeekInfo.year} when these
                                            changes are saved.
                                        </div>
                                    </div>
                                </div>
                            </div>
                        );
                    } catch (error) {
                        return (
                            <div
                                className="alert alert-warning mb-3"
                                role="alert"
                            >
                                <div className="d-flex align-items-center">
                                    <i className="fas fa-exclamation-triangle me-2"></i>
                                    <div>
                                        <strong>
                                            Project Extension Required
                                        </strong>
                                        <div className="small mt-1">
                                            One or more phases extend beyond the
                                            current project duration.
                                        </div>
                                    </div>
                                </div>
                            </div>
                        );
                    }
                }
                return null;
            })()}

            {editablePhases.length === 0 ? (
                <p className="text-muted">No phases defined yet</p>
            ) : (
                <table className="table">
                    <thead>
                        <tr>
                            <th style={{width: '40px'}}></th>
                            <th>Name</th>
                            <th>Type</th>
                            <th>Colour</th>
                            <th>Start Week</th>
                            <th>Duration (Weeks)</th>
                            {canCurrentUserEdit && <th>Actions</th>}
                        </tr>
                    </thead>
                    <tbody>
                        {editablePhases.map((phase: EditablePhase) => {
                            const isExpanded = expandedPhases.has(phase.id);

                            return (
                                <React.Fragment key={phase.id}>
                                    <tr
                                        className={
                                            phase.isEditing
                                                ? 'phase-editing-row'
                                                : ''
                                        }
                                        style={{
                                            borderLeft: phase.colour
                                                ? `5px solid ${phase.colour}`
                                                : undefined
                                        }}
                                    >
                                        <td>
                                            {!phase.isNew && (
                                                <Button
                                                    color="link"
                                                    size="sm"
                                                    className="p-0 text-muted"
                                                    onClick={() =>
                                                        togglePhaseExpansion(
                                                            phase.id
                                                        )
                                                    }
                                                    title={
                                                        isExpanded
                                                            ? 'Hide description'
                                                            : 'Show description'
                                                    }
                                                >
                                                    {isExpanded ? (
                                                        <ChevronDown
                                                            size={14}
                                                        />
                                                    ) : (
                                                        <ChevronRight
                                                            size={14}
                                                        />
                                                    )}
                                                </Button>
                                            )}
                                        </td>
                                        <td>
                                            {phase.isEditing ? (
                                                <div>
                                                    <Input
                                                        value={phase.name}
                                                        onChange={e =>
                                                            updatePhaseField(
                                                                phase.id,
                                                                'name',
                                                                e.target.value
                                                            )
                                                        }
                                                        placeholder="Phase name"
                                                        bsSize="sm"
                                                    />
                                                    <FieldError
                                                        path={`phases.${phase.index}.name`}
                                                        error={
                                                            bulkPhaseUpdate.error
                                                        }
                                                    />
                                                </div>
                                            ) : (
                                                phase.name
                                            )}
                                        </td>
                                        <td>
                                            {phase.isEditing ? (
                                                <div>
                                                    <Select
                                                        value={phaseTypeOptions.find(
                                                            opt =>
                                                                opt.value ===
                                                                phase.type
                                                        )}
                                                        onChange={selected =>
                                                            updatePhaseField(
                                                                phase.id,
                                                                'type',
                                                                selected?.value
                                                            )
                                                        }
                                                        options={
                                                            phaseTypeOptions
                                                        }
                                                        classNamePrefix="react-select"
                                                        className="react-select-sm"
                                                    />
                                                    <FieldError
                                                        path={`phases.${phase.index}.type`}
                                                        error={
                                                            bulkPhaseUpdate.error
                                                        }
                                                    />
                                                </div>
                                            ) : (
                                                PhaseType[phase.type]
                                            )}
                                        </td>
                                        <td>
                                            {phase.isEditing ? (
                                                <Input
                                                    type="color"
                                                    value={
                                                        phase.colour ||
                                                        '#FFFFFF'
                                                    }
                                                    onChange={e =>
                                                        updatePhaseField(
                                                            phase.id,
                                                            'colour',
                                                            e.target.value
                                                        )
                                                    }
                                                    className="form-control-sm"
                                                    style={{
                                                        width: '50px',
                                                        height: '31px',
                                                        padding: '0.1rem 0.2rem'
                                                    }}
                                                />
                                            ) : (
                                                <div
                                                    style={{
                                                        width: '20px',
                                                        height: '20px',
                                                        backgroundColor:
                                                            phase.colour ||
                                                            'transparent',
                                                        border: '1px solid #ccc',
                                                        borderRadius: '3px'
                                                    }}
                                                />
                                            )}
                                        </td>
                                        <td>
                                            {phase.isEditing ? (
                                                <div>
                                                    <WeekSelector
                                                        value={
                                                            phase.startGlobalWeekNumber
                                                        }
                                                        onChange={globalWeekNumber => {
                                                            updatePhaseWeekData(
                                                                phase.id,
                                                                globalWeekNumber,
                                                                phase.durationInWeeks
                                                            );
                                                        }}
                                                        label=""
                                                        className="mb-0"
                                                    />
                                                    <FieldError
                                                        path={`phases.${phase.index}.startGlobalWeekNumber`}
                                                        error={
                                                            bulkPhaseUpdate.error
                                                        }
                                                    />
                                                </div>
                                            ) : (
                                                (() => {
                                                    const weekInfo =
                                                        getPhaseWeekInfo(phase);
                                                    if (weekInfo) {
                                                        return (
                                                            <div>
                                                                <div>
                                                                    Week{' '}
                                                                    {
                                                                        weekInfo
                                                                            .startWeekInfo
                                                                            .weekOfYear
                                                                    }{' '}
                                                                    of{' '}
                                                                    {
                                                                        weekInfo
                                                                            .startWeekInfo
                                                                            .year
                                                                    }
                                                                </div>
                                                                <small className="text-muted">
                                                                    {formatDate(
                                                                        weekInfo
                                                                            .startWeekInfo
                                                                            .mondayOfWeek
                                                                    )}
                                                                </small>
                                                            </div>
                                                        );
                                                    }
                                                    return formatDate(
                                                        phase.startOn
                                                    );
                                                })()
                                            )}
                                        </td>
                                        <td>
                                            {phase.isEditing ? (
                                                <div>
                                                    <Input
                                                        type="number"
                                                        value={
                                                            phase.durationInWeeks ||
                                                            ''
                                                        }
                                                        onChange={e => {
                                                            const duration =
                                                                parseInt(
                                                                    e.target
                                                                        .value,
                                                                    10
                                                                ) || 1;
                                                            updatePhaseWeekData(
                                                                phase.id,
                                                                phase.startGlobalWeekNumber,
                                                                duration
                                                            );
                                                        }}
                                                        min="1"
                                                        max={
                                                            project.durationInWeeks
                                                        }
                                                        bsSize="sm"
                                                        style={{width: '80px'}}
                                                    />
                                                    <FieldError
                                                        path={`phases.${phase.index}.durationInWeeks`}
                                                        error={
                                                            bulkPhaseUpdate.error
                                                        }
                                                    />
                                                    {(() => {
                                                        const weekInfo =
                                                            getPhaseWeekInfo(
                                                                phase
                                                            );
                                                        if (weekInfo) {
                                                            return (
                                                                <FormText>
                                                                    Ends Week{' '}
                                                                    {
                                                                        weekInfo
                                                                            .endWeekInfo
                                                                            .weekOfYear
                                                                    }{' '}
                                                                    of{' '}
                                                                    {
                                                                        weekInfo
                                                                            .endWeekInfo
                                                                            .year
                                                                    }
                                                                    <br />
                                                                    <small className="text-muted">
                                                                        (Sun,{' '}
                                                                        {formatDate(
                                                                            addDays(
                                                                                weekInfo
                                                                                    .endWeekInfo
                                                                                    .mondayOfWeek,
                                                                                6
                                                                            )
                                                                        )}
                                                                        )
                                                                    </small>
                                                                </FormText>
                                                            );
                                                        }
                                                        return null;
                                                    })()}
                                                </div>
                                            ) : (
                                                <div>
                                                    <div>
                                                        {phase.durationInWeeks}{' '}
                                                        week
                                                        {phase.durationInWeeks !==
                                                        1
                                                            ? 's'
                                                            : ''}
                                                    </div>
                                                    <small className="text-muted">
                                                        Ends{' '}
                                                        {formatDate(
                                                            phase.endOn
                                                        )}
                                                    </small>
                                                </div>
                                            )}
                                        </td>
                                        {canCurrentUserEdit && (
                                            <td className="phase-actions">
                                                <div className="btn-group-inline">
                                                    <Button
                                                        color="link"
                                                        size="sm"
                                                        className="p-0"
                                                        onClick={() =>
                                                            togglePhaseEdit(
                                                                phase.id
                                                            )
                                                        }
                                                        title={
                                                            phase.isEditing
                                                                ? 'Cancel editing'
                                                                : 'Edit phase'
                                                        }
                                                    >
                                                        {phase.isEditing ? (
                                                            <X size={14} />
                                                        ) : (
                                                            <Edit size={14} />
                                                        )}
                                                    </Button>
                                                </div>
                                            </td>
                                        )}
                                    </tr>
                                    {/* Description section - for both editing and non-editing modes */}
                                    {isExpanded && !phase.isNew && (
                                        <tr>
                                            <td colSpan={6} className="p-0">
                                                {/* Description section */}
                                                {(phase.isEditing ||
                                                    phase.description) && (
                                                    <div className="p-3 bg-light border-bottom">
                                                        <small className="text-muted d-block mb-1">
                                                            Description:
                                                        </small>
                                                        {phase.isEditing ? (
                                                            <FormGroup className="mb-0">
                                                                <Input
                                                                    type="textarea"
                                                                    value={
                                                                        phase.description ||
                                                                        ''
                                                                    }
                                                                    onChange={e =>
                                                                        updatePhaseField(
                                                                            phase.id,
                                                                            'description',
                                                                            e
                                                                                .target
                                                                                .value
                                                                        )
                                                                    }
                                                                    placeholder="Phase description"
                                                                    rows={3}
                                                                    bsSize="sm"
                                                                />
                                                            </FormGroup>
                                                        ) : (
                                                            <div>
                                                                {
                                                                    phase.description
                                                                }
                                                            </div>
                                                        )}
                                                    </div>
                                                )}
                                            </td>
                                            {canCurrentUserEdit && (
                                                <td>
                                                    {phase.isEditing &&
                                                        !phase.isNew && (
                                                            <div className="d-flex justify-content-start">
                                                                <Button
                                                                    color="link"
                                                                    size="sm"
                                                                    className="p-0 text-danger me-2"
                                                                    onClick={() =>
                                                                        handleDeletePhase(
                                                                            phase.id
                                                                        )
                                                                    }
                                                                    disabled={
                                                                        deletePhase.isPending
                                                                    }
                                                                    title="Delete phase"
                                                                >
                                                                    <Trash2
                                                                        size={
                                                                            14
                                                                        }
                                                                    />
                                                                </Button>
                                                            </div>
                                                        )}
                                                </td>
                                            )}
                                        </tr>
                                    )}
                                </React.Fragment>
                            );
                        })}
                    </tbody>
                </table>
            )}

            {/* Reason for Change Modal */}
            <ReasonForChangeModal
                isOpen={isReasonModalOpen}
                onClose={() => {
                    setIsReasonModalOpen(false);
                    setPendingUpdates([]);
                }}
                onSubmit={submitUpdates}
                description={`update ${pendingUpdates.length} phase${pendingUpdates.length > 1 ? 's' : ''}`}
                isPending={bulkPhaseUpdate.isPending}
                projectStatus={project?.status}
            />

            {/* Delete Phase Confirmation Modal */}
            <ReasonForChangeModal
                isOpen={isDeleteModalOpen}
                onClose={() => {
                    setIsDeleteModalOpen(false);
                    setPhaseToDelete(null);
                }}
                onSubmit={confirmDeletePhase}
                description="delete this phase"
                isPending={deletePhase.isPending}
                projectStatus={project?.status}
            />
        </div>
    );
};

export default PhasesSection;
